import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Form } from 'devextreme-react'
import { Item, Label, PatternRule, RequiredRule, SimpleItem } from 'devextreme-react/form'
import { RootState } from '../../../../redux/combineReducers'
import { useScreenSize } from '../../../../utils/media-query'
import { addEnlaces, addParentEnlaces } from '../redux/Enlace/enlaceActions'
import { EnlaceModel } from '../models/enlaceModel'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'
import { EnlacesModel } from '../models/enlacesModel'
import { useForm } from 'react-hook-form'
import { addCompany } from '../redux/companiesActions'
import { postCompany, putCompany } from '../services/companiesServices'
import { addDataFormChange } from '../../../../redux/actions/sharedUtilsActions'
import { getEnlaces } from '../services/enlacesService'
import { refreshGrid } from '../../../../utils/sharedUitls'

function EnlacesFormComponent(props: any): ReactElement {
  const userState = useSelector((state: RootState) => state.user)
  const companyState = useSelector((state: RootState) => state.company)
  const company = useSelector((state: RootState) => state.company?.company)
  const calculadoraState = useSelector(
    (state: RootState) =>
      state.calculadoraTransferencias?.calculadoraTransferencias,
  )
  const emailState = useSelector((state: RootState) => state.email?.email)
  const enlaceState = useSelector((state: RootState) => state.enlace)
  const isChangedState = useSelector(
    (state: RootState) => state.sharedUtils.isChanged,
  )
  const { isLarge } = useScreenSize()
  const [loading, setLoading] = useState(false)
  const { register, handleSubmit } = useForm()
  const formData = useRef({})
  const dispatch = useDispatch()
  useEffect(() => {
    async function fetchData() {
      try {
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [])
  const onSubmit = async (data: any) => {
    setLoading(true)
    let result: boolean = false
    if (!company?.Id) {
      dispatch(addCompany({ ...company, Id: 0 }))
    }
    if (company?.Id) {
      result = await putCompany(
        userState!.user!,
        company,
        calculadoraState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    } else {
      result = await postCompany(
        userState!.user!,
        company!,
        calculadoraState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    }

    if (result) {
      setLoading(false)
      refreshGrid(props, true)
      props.setShowForm(false)
      localStorage.setItem(
        'parentCompanyModulos',
        JSON.stringify(companyState?.companyModulos),
      )
      localStorage.setItem(
        'parentEnlaces',
        JSON.stringify(
          dispatch(
            addParentEnlaces(
              await getEnlaces(
                userState?.user!,
                parseInt(userState?.user!.CompanyId!),
              ),
            ),
          ).payload,
        ),
      )
    } else {
      setLoading(false)
    }
  }
  const handleUpdatedEnlace = (property: any) => {
    dispatch(
      addEnlaces({
        ...enlaceState?.enlaces,
        ...property,
      }),
    )
  }

  function getEnlaceValue(codigoEnlace: string) {
    let value = ''
    if (enlaceState?.enlaces!) {
      var doubles = enlaceState?.enlaces.map(function (x: EnlaceModel) {
        if (x.CodigoTipoEnlace == codigoEnlace) {
          value = x.EnlaceValue
        }
      })
    }
    return value
  }

  //comprobaremos si existe el enlace y lo añadiremos o lo updatearemos
  function addEnlace(enlace: EnlaceModel) {
    if (enlaceState?.enlaces) {
      let exitEnlace = false
      enlaceState?.enlaces.map(function (x: EnlaceModel) {
        if (enlace.CodigoTipoEnlace == x.CodigoTipoEnlace) {
        }
      })
    }
  }
  const handleCancelClick = () => {
    dispatch(addEnlaces(undefined))
    props.setShowForm(false)
    /*  calculadoraFormRef.current.instance.resetValues() */
  }
  const renderButtons = () => {
    /*  if (!props.showForm) {
      enlacesFormRef.current.instance.resetValues()
    } */
    return renderButtonsComponent(`enlacesData`, handleCancelClick)
  }
  const enlacesFormRef: any = useRef(null)
  if(!props.showForm){
    if(enlacesFormRef!=null){
 
      enlacesFormRef.current?.instance.resetValues()
  }

  }
  return (
    <>
      <div
        className={'content-block responsive-paddings'}
        style={{ borderColor: '#f4f9ff', borderWidth: '2px', margin: '0.6%' }}
      >
        <div className={'form'}>
          <form
            className={'legalForm'}
            onSubmit={handleSubmit(onSubmit)}
            style={{ width: '100%' }}
          >
            <Form
              formData={formData.current}
              disabled={loading}
              ref={enlacesFormRef}
              labelLocation={isLarge ? 'left' : 'top'}
              validationGroup="enlacesData"
            >
              <Item
                dataField={'linkLegal'}
                editorType={'dxTextBox'}
                editorOptions={{
                  disabled: false,
                  stylingMode: 'filled',
                  placeholder: 'Link a Legal',
                  value: getEnlaceValue('Legal'),
                  mode: 'text',
                  onValueChanged: (event: any) => {
                    if (event.event) {
                      let enlace: EnlaceModel | undefined =
                        enlaceState?.enlaces?.find(
                          (x) =>
                            x.IdTipoEnlace == 1 &&
                            x.IdCompany == companyState?.company?.Id,
                        ) as never
                      if (enlace == undefined) {
                        enlace = {
                          Id: 0,
                          IdCompany: companyState?.company?.Id!,
                          IdTipoEnlace: 1,
                          EnlaceValue: event.value,
                          CodigoTipoEnlace: 'Legal',
                        }
                      } else {
                        const index = enlaceState?.enlaces.indexOf(enlace)
                        enlace = enlace as EnlaceModel
                        enlace.EnlaceValue = event.value
                        enlaceState?.enlaces.splice(index!, 1)
                      }

                      enlaceState?.enlaces?.push(enlace as never)
                      dispatch(addEnlaces(enlaceState?.enlaces!))
                      if (!isChangedState) dispatch(addDataFormChange(true))
                    }
                  },
                }}
              >
                <RequiredRule message="El campo es requerido" />
<PatternRule message="Debe introducir una URL. ex: https://www.ejemplo.es " pattern="https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)"/>
                <Label visible={true} text="Link a Legal" />
              </Item>

              <Item
                dataField={'linkPrivacidadCookies'}
                editorType={'dxTextBox'}
                editorOptions={{
                  disabled: false,
                  stylingMode: 'filled',
                  placeholder: 'Link a términos de privacidad y cookies',
                  value: getEnlaceValue('Cookies'),
                  mode: 'text',
                  onValueChanged: (event: any) => {
                    if (event.event) {
                      let enlace: EnlaceModel | undefined =
                        enlaceState?.enlaces?.find(
                          (x) =>
                            x.IdTipoEnlace == 2 &&
                            x.IdCompany == companyState?.company?.Id,
                        ) as never
                      if (enlace == undefined) {
                        enlace = {
                          Id: 0,
                          IdCompany: companyState?.company?.Id!,
                          IdTipoEnlace: 2,
                          EnlaceValue: event.value,
                          CodigoTipoEnlace: 'Cookies',
                        }
                      } else {
                        const index = enlaceState?.enlaces.indexOf(enlace)
                        enlace = enlace as EnlaceModel
                        if (event.value == '') {
                          enlace.EnlaceValue = event.previousValue
                          enlace.CodigoTipoEnlace = 'Cookies'
                        } else {
                          enlace.EnlaceValue = event.value
                          enlace.CodigoTipoEnlace = 'Cookies'
                        }
                        enlaceState?.enlaces.splice(index!, 1)
                      }

                      enlaceState?.enlaces?.push(enlace as never)

                      dispatch(addEnlaces(enlaceState?.enlaces!))
                      if (!isChangedState) dispatch(addDataFormChange(true))
                    }
                  },
                }}
              >
                <RequiredRule message="El campo es requerido" />
                <PatternRule message="Debe introducir una URL. ex: https://www.ejemplo.es " pattern="https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)"/>

                <Label
                  visible={true}
                  text="Link a términos de privacidad y cookies"
                />
              </Item>
              <Item
                dataField={'LinkRGPD'}
                editorType={'dxTextBox'}
                editorOptions={{
                  disabled: false,
                  stylingMode: 'filled',
                  placeholder: 'Link a RGPD',
                  value: getEnlaceValue('RGPD'),
                  mode: 'text',
                  onValueChanged: (event: any) => {
                    if (event.event) {
                      let enlace: EnlaceModel | undefined =
                        enlaceState?.enlaces?.find(
                          (x) =>
                            x.IdTipoEnlace == 3 &&
                            x.IdCompany == companyState?.company?.Id,
                        ) as never
                      if (enlace == undefined) {
                        enlace = {
                          Id: 0,
                          IdCompany: companyState?.company?.Id!,
                          IdTipoEnlace: 3,
                          EnlaceValue: event.value,
                          CodigoTipoEnlace: 'RGPD',
                        }
                      } else {
                        const index = enlaceState?.enlaces.indexOf(enlace)
                        enlace = enlace as EnlaceModel
                        enlace.EnlaceValue = event.value
                        enlaceState?.enlaces.splice(index!, 1)
                      }

                      enlaceState?.enlaces?.push(enlace as never)
                      dispatch(addEnlaces(enlaceState?.enlaces!))
                      if (!isChangedState) dispatch(addDataFormChange(true))
                    }
                  },
                }}
              >
                <RequiredRule message="El campo es requerido" />
                <PatternRule message="Debe introducir una URL. ex: https://www.ejemplo.es " pattern="https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)"/>

                <Label visible={true} text="Link a RGPD" />
              </Item>
           {/*    <SimpleItem render={renderButtons} cssClass="alignBottom" /> */}
            </Form>
          </form>
        </div>
      </div>
    </>
  )
}

export default EnlacesFormComponent
