import { RoleModel } from '../../../../models/roleModel'
import { UsuarioCampoDatoModel } from '../models/usuarioCampoDatoModel'
import { UsuarioModel } from '../models/usuarioModel'
import {
  ADD_USUARIOS,
  ADD_USUARIO,
  CLEAR_USUARIO,
  UsuarioActionsTypes,
  ADD_ROLES,
} from './usuarioType'

export const addUsuarios = (
  company: UsuarioCampoDatoModel | undefined,
): UsuarioActionsTypes => ({
  type: ADD_USUARIOS,
  payload: company,
})
export const addUsuario = (
  usuario: Partial<UsuarioModel> | UsuarioModel | undefined,
): UsuarioActionsTypes => ({
  type: ADD_USUARIO,
  payload: usuario,
})

export const clearUsuario = (
  usuario: UsuarioModel | undefined,
): UsuarioActionsTypes => ({
  type: CLEAR_USUARIO,
  payload: usuario,
})
export const addRoles = (
  roles: RoleModel[] | undefined,
): UsuarioActionsTypes => ({
  type: ADD_ROLES,
  payload: roles,
})
