import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  Button,
  Popup,
  ScrollView,
  SelectBox,
  TreeList,
} from 'devextreme-react'
import './plantillasAdministracionStyles.scss'
import { RootState } from '../../../../redux/combineReducers'
import { getStructure } from '../services/componenteServices'
import { ButtonColumnModel, DataGridModel } from 'shared-models'
import { useHistory } from 'react-router-dom'
import { SharedGrid } from 'react-library-grid'
import { ComponenteCampoDatoModel } from '../models/componenteModel/componenteDatoModelModel'
import NotFound from '../../../../components/notFound/notFound'
import { addModulos } from '../redux/modulo/moduloActions'
import { getModulos } from '../services/moduloServices'
import {
  deletePlantilla,
  getMapperPlantilla,
  getPlantillaCampos,
  getPlantillaWithChild,
} from '../services/plantillaService'
import {
  Column,
  ColumnChooser,
  HeaderFilter,
  SearchPanel,
} from 'devextreme-react/data-grid'
import { PlantillaModel } from '../models/plantillaModel/plantillaModel'
import { isOpenPopup } from '../../../../redux/actions/sharedUtilsActions'
import PlantillaFormPage from './plantillaFormPage'
import { getPerfiles } from '../../../administracionPages/perfilPage/service/perfilService'
import {
  addPerfil,
  addPerfiles,
} from '../../../administracionPages/perfilPage/redux/perfilActions'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'
import { CompanyModuloModel } from '../../companyPage/models/companyModuloModel'
import {
  addPlantilla,
  addPlantillaCampos,
} from '../redux/plantilla/plantillaComponenteActions'
import { useScreenSize } from '../../../../utils/media-query'

function PlantillaPage(): ReactElement {
  const userState = useSelector((state: RootState) => state.user)
  const [structure, setStructure] = useState(new ComponenteCampoDatoModel())
  const moduloState = useSelector((state: RootState) => state.modulo)
  const moduloComponenteState = useSelector(
    (state: RootState) => state.moduloComponente,
  )
  const perfilState = useSelector((state: RootState) => state.perfil)
  const [selectedModulo, setModulo] = useState(0)
  const [showForm, setShowForm] = useState(false)
  const [rowFocused, setRowFocused] = useState('')
  const [rowSelected, setRow] = useState(0)
  const [plantillas, setPlantillas] = useState<PlantillaModel[]>([])
  const [plantillaSelected, setPlantilla] = useState(
    plantillas.length > 0 ? plantillas[0] : new PlantillaModel(),
  )
  const gridRef: any = useRef(null)
  const formData = useRef({})
  const dispatch = useDispatch()
  const history = useHistory()
  const popupPlantillaRef = useRef(null)
  const [modulosParent, setModulosParent] = useState([{}])
  const { isLarge, isMedium, isSmall, isXSmall } = useScreenSize()

  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  useEffect(() => {
    async function fetchData() {
      setStructure(await getStructure(userState!.user!))
      dispatch(addModulos(await getModulos(userState?.user!)))
      dispatch(addPerfiles(await getPerfiles(userState!.user!)))
      setModulosParent(
        jsonConvert.deserializeArray(
          JSON.parse(localStorage.getItem('parentCompanyModulos')!),
          CompanyModuloModel,
        ),
      )
    }
    fetchData()
  }, [])

  const handleOpenCreatePopup = async () => {
    if (plantillas.length > 0) {
      dispatch(addPlantilla(new PlantillaModel()))
      dispatch(
        addPlantillaCampos(
          await getPlantillaCampos(userState!.user!, plantillas[0].Id),
        ),
      )
      setShowForm(!showForm)
    } else {
      showToast(
        'Selecciona un componentes y su plantilla',
        NotifyType.info,
        5000,
      )
    }
  }
  const editButtonPlantillas = (cellRender: any) => {
    return (
      <Button
        icon="edit"
        text=""
        hint="Editar"
        onClick={async () => {
          dispatch(addPerfil({}))
          dispatch(addPlantilla(new PlantillaModel()))
          dispatch(
            addPlantillaCampos(
              await getPlantillaCampos(userState!.user!, cellRender.row.key),
            ),
          )
          dispatch(
            addPlantilla(
              plantillas.find(
                (plantilla) => plantilla.Id == cellRender.row.key,
              ),
            ),
          )
          setShowForm(!showForm)
        }}
      ></Button>
    )
  }
  const deleteButtonPlantillas = (cellRender: any) => {
    return (
      <Button
        icon="trash"
        text=""
        hint="Eliminar"
        onClick={async () => {
          if (cellRender.row.data.IdPlantillaParent) {
            await deletePlantilla(userState!.user!, cellRender.row.key)
            const plantillasChild = await getPlantillaWithChild(
              userState!.user!,
              plantillas[0].Id as number,
            )
            setPlantillas(plantillasChild)
          } else
            showToast(
              'No puedes eliminar una plantilla generica',
              NotifyType.warning,
              5000,
            )
        }}
      ></Button>
    )
  }
  function fillDataGrid(Id: Number): DataGridModel {
    const buttonColumn: ButtonColumnModel = new ButtonColumnModel({
      icon: 'edit',
      text: '',
      hint: 'Editar',
      cellRender: async (row: any) => {
        setRow(row.data)
        const plantillasChild = await getPlantillaWithChild(
          userState!.user!,
          row.data.IdPlantilla,
        )
        setPlantillas(plantillasChild)

        setPlantilla(
          getMapperPlantilla(
            plantillasChild.length > 0
              ? plantillasChild[0]
              : new PlantillaModel(),
          ),
        )
      },
    })
    let gridheight = '600;'
    gridheight = isXSmall ? '300' : '600'
    gridheight = isSmall ? '400' : '600'
    gridheight = isMedium ? '400' : '400'
    gridheight = isLarge ? '400' : '400'
    let dataGridModel = new DataGridModel({
      dataGridName: 'componenteGridComponent',
      height: gridheight,
      ref: gridRef,
      rowFocused: rowFocused,
      columns: structure.Campos,
      openRow: ({}) => {},
      openRowEnterKey: ({}) => {},
      focusEventChanged: (e: any) => {
        if (e.row) {
          setRowFocused(e.row.key)
        }
      },
      focusEventChanging: ({}) => {},
      openCreatePopup: undefined,
      user: userState!.user!,
      url: `${process.env.REACT_APP_API_URL_CORE}/ModuloComponente/` + Id,
      urlAux: `${process.env.REACT_APP_API_URL_CORE}/ModuloComponente/` + Id,
      buttonsColumns: [buttonColumn],
      filterRow: true,
      filterPanel: true,
      columnChooser: true,
      headerFilter: true,
      exportData: true,
      showSearchPanel: true,
      showToolbar: true,
      history: history,
      singularName: 'Componente',
      pluralName: 'Componentes',
      pageName: 'MuduloComponente',
      openedPopup: showForm,
    })

    return dataGridModel
  }

  return (
    <>
      {true ? (
        <div
          className={'content-block dx-card'}
          style={{
            borderColor: '#f4f9ff',
            borderWidth: '2px',
            margin: '0.3%',
            padding: '0.16%',
          }}
        >
          {structure?.Campos != undefined && structure?.Campos.length > 0 ? (
            <div>
              <h2>Plantillas</h2>
              <SelectBox
                items={modulosParent}
                displayExpr="Nombre"
                valueExpr="IdModulo"
                placeholder="Selecciona uno de los Modulo contratados"
                //value={0}
                onValueChanged={async (e: any) => {
                  setModulo(e.value)
                  setPlantillas([])
                  const refreshButton = document.getElementsByClassName(
                    'dx-icon dx-icon-refresh',
                  )[0] as HTMLElement
                  refreshButton.click()
                }}
              />
              <h5>Vistas cargadas del modulo</h5>
              <ScrollView direction="vertical">
                <div style={{ marginBottom: '6vh' }}>
                  <SharedGrid dataGridModel={fillDataGrid(selectedModulo)} />
                </div>
              </ScrollView>
              <div>
                <Button
                  icon="save"
                  text="Nueva Plantillas"
                  type="success"
                  stylingMode="contained"
                  style={{ float: 'right', margin: '1em' }}
                  onClick={handleOpenCreatePopup}
                />
              </div>
              <h5>
                Nueva Plantilla hereda de:
                <b>
                  {plantillas.length > 0
                    ? plantillas[0].Nombre
                    : '(Selecciona una vista para poder cargas sus plantillas)'}
                </b>
              </h5>
              <TreeList
                dataSource={plantillas != undefined ? plantillas : []}
                focusedRowEnabled={true}
                showBorders={true}
                columnAutoWidth={true}
                wordWrapEnabled={true}
                defaultExpandedRowKeys={[1]}
                defaultSelectedRowKeys={[1]}
                keyExpr="Id"
                parentIdExpr="IdPlantillaParent"
                id="Id"
                onFocusedRowChanged={(e: any) => {
                  setPlantilla(getMapperPlantilla(e.row.data))
                }}
              >
                <SearchPanel visible={true} width={250} />
                <HeaderFilter visible={true} />
                <ColumnChooser enabled={true} />
                <Column
                  type="buttons"
                  cellRender={(cellRenderData: any) => {
                    return editButtonPlantillas(cellRenderData)
                  }}
                />
                <Column
                  type="buttons"
                  cellRender={(cellRenderData: any) => {
                    return deleteButtonPlantillas(cellRenderData)
                  }}
                />
                <Column dataField="Nombre" />
                <Column dataField="EsGenerica" />
              </TreeList>

              <Popup
                visible={showForm}
                ref={popupPlantillaRef}
                onHiding={({}) => {
                  setShowForm(!showForm)
                  dispatch(isOpenPopup(false, popupPlantillaRef))
                }}
                dragEnabled={true}
                closeOnOutsideClick={false}
                showTitle={true}
                title="Creación Plantilla"
                resizeEnabled={true}
                showCloseButton={true}
                width="95%"
                height="95%"
              >
                <ScrollView width="100%" height="100%">
                  <PlantillaFormPage
                    row={rowSelected}
                    setShowForm={setShowForm}
                    showForm={showForm}
                    setPlantillas={setPlantillas}
                    plantillaParent={plantillas.find(
                      (x) => x.EsGenerica === true,
                    )}
                  ></PlantillaFormPage>
                </ScrollView>
              </Popup>
            </div>
          ) : null}
        </div>
      ) : (
        <div>
          <NotFound data={{ type: 1 }}></NotFound>
        </div>
      )}
    </>
  )
}
export default PlantillaPage
