import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'

import {
  checkStatusCode,
  customLog,
  logType,
  methodsEndPointType,
} from 'shared-utils'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { UsuarioCampoDatoModel } from '../models/usuarioCampoDatoModel'
import { UsuarioModel } from '../models/usuarioModel'
import { authHeader } from 'shared-services'
import { UserModel } from 'shared-models'

const serviceName = 'UsuarioService'

export const getUsuarios = async (
  user: UserModel,
): Promise<UsuarioCampoDatoModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/usuario`
    let usuario: UsuarioCampoDatoModel = new UsuarioCampoDatoModel()
    let datos
    await axios({
      url: url,
      method: methodsEndPointType.GET,
      headers: authHeader(user),
    })
      .then((response) => {
        usuario = jsonConvert.deserializeObject(
          response.data,
          UsuarioCampoDatoModel,
        )
      })
      .catch((error) => {
        customLog(
          null,
          logType.ERROR,
          serviceName,
          getUsuarios.name,
          methodsEndPointType.GET,
        )
        showToast(
          'No se han podido obtener los usuarios.',
          NotifyType.error,
          5000,
        )
        checkStatusCode(error.request.status)
      })

    return usuario
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
export async function getUsuario(
  user: UserModel,
  id: number,
): Promise<UsuarioModel> {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let url = `${process.env.REACT_APP_API_URL_CORE}/usuario/${id}`

  let usuario: UsuarioModel = new UsuarioModel()
  await axios({
    url: url,
    method: methodsEndPointType.GET,
    headers: authHeader(user),
  })
    .then((response) => {
      checkStatusCode(response.status)
      usuario = jsonConvert.deserializeObject(
        response.data.Datos[0],
        UsuarioModel,
      )
      usuario.Enabled = usuario.Enabled ? false : true
    })
    .catch((error) => {
      customLog(
        null,
        logType.ERROR,
        serviceName,
        getUsuario.name,
        methodsEndPointType.GET,
      )
      showToast('No se ha podido obtener el usuario.', NotifyType.error, 5000)
      checkStatusCode(error.request.status)
    })

  return usuario
}
