export async function postUsuario(
  user: UserModel,
  usuario: UsuarioModel,
): Promise<UsuarioModel | undefined | Boolean | any | null> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/usuario`

    let usuarioData: UsuarioModel | null = new UsuarioModel()
    if (usuario.RolesTipoCuenta.length > 0) {
      usuario.IdRole = 0
    }
    usuario.Enabled = usuario.Enabled ? false : true

    if (usuario.IdPerfil == 0) delete usuario.IdPerfil

    await axios
      .post(url, usuario, { headers: authHeader(user) })
      .then((response) => {
        checkStatusCode(response.status)
        usuarioData = jsonConvert.deserializeObject(response.data, UsuarioModel)
        customLog(
          `Usuario created ${response.data.Id}`,
          logType.INFO,
          serviceName,
          postUsuario.name,
          methodsEndPointType.POST,
        )
        showToast('Usuario creado', NotifyType.success, 5000)
      })
      .catch((error) => {
        showToast(error.response.data.Message, NotifyType.error, 5000)
        customLog(
          `Error create user  => ${error.message}`,
          logType.ERROR,
          serviceName,
          postUsuario.name,
          methodsEndPointType.PUT,
        )
        checkStatusCode(error.request.status)
        usuarioData = null
      })
    // const response: Boolean = true
    // const data: UsuarioModel = usuarioData
    return usuarioData
    //return [response, data]
  } catch (error: any) {
    showToast(error.response.data, NotifyType.error, 5000)
    customLog(
      `Error created user => ${error.message}`,
      logType.ERROR,
      serviceName,
      postUsuario.name,
      methodsEndPointType.POST,
    )
    showToast(error.response.data, NotifyType.error, 5000)
  }
}
export async function putUsuario(
  user: UserModel,
  usuario: UsuarioModel,
): Promise<UsuarioModel | undefined | Boolean | any> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/usuario`

    let usuarioData: UsuarioModel | null = new UsuarioModel()
    if (usuario.RolesTipoCuenta?.length > 0) {
      usuario.IdRole = 0
    }
    usuario.Enabled = usuario.Enabled ? false : true
    if (usuario.IdPerfil == 0) delete usuario.IdPerfil
    await axios
      .put(
        url,
        {
          ...usuario,
          IdPerfil: usuario.IdPerfil != 0 ? usuario.IdPerfil : null,
        },
        { headers: authHeader(user) },
      )
      .then((response) => {
        checkStatusCode(response.status)
        usuarioData = jsonConvert.deserializeObject(response.data, UsuarioModel)
        customLog(
          `User updated ${usuario.Id}`,
          logType.INFO,
          serviceName,
          putUsuario.name,
          methodsEndPointType.PUT,
        )
        showToast('Usuario editado', NotifyType.success, 5000)
      })
      .catch((error) => {
        showToast(error.response.data.Message, NotifyType.error, 5000)
        customLog(
          `Error updated user  => ${error.message}`,
          logType.ERROR,
          serviceName,
          putUsuario.name,
          methodsEndPointType.PUT,
        )
        checkStatusCode(error.request.status)
        usuarioData = null
      })
    // const response: Boolean = true
    // const data: UsuarioModel = usuarioData
    // return [response, data]
    return usuarioData
  } catch (error: any) {
    showToast(error.response.data, NotifyType.error, 5000)
  }
}
