export async function deleteUsuario(user: UserModel, idUsuario: number) {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/Usuario/RelatedDeleteUser/${idUsuario}`
    let url2 = `${process.env.REACT_APP_API_URL_CORE}/Usuario/${idUsuario}`
    await axios.delete(url, { headers: authHeader(user!) }).then((response) => {
      axios.delete(url2, { headers: authHeader(user!) }).then((response) => {
        checkStatusCode(response.status)
        customLog(
          `usuario elminado`,
          logType.INFO,
          serviceName,
          deleteUsuario.name,
          methodsEndPointType.POST,
        )
        showToast('Usuario elminada', NotifyType.success, 5000)
      })
      checkStatusCode(response.status)
      customLog(
        `Usuario elminada`,
        logType.INFO,
        serviceName,
        deleteUsuario.name,
        methodsEndPointType.POST,
      )
      showToast('Usuario elminada', NotifyType.success, 5000)
    })
    return true
  } catch (error: any) {
    if (error.response.status == 400) {
      showToast(error.response.data, NotifyType.error, 5000)
    }
    customLog(
      `Error al eliminar Compañia => ${error.response.data}`,
      logType.ERROR,
      serviceName,
      deleteUsuario.name,
      methodsEndPointType.POST,
    )
    checkStatusCode(error.request.status)
    return false
  }
}

export function getMapperUsuario(usuarioData: any): UsuarioModel {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let usuario: UsuarioModel = jsonConvert.deserializeObject(
    usuarioData,
    UsuarioModel,
  )
  return usuario
}
