import { RoleModel } from '../../../../models/roleModel'
import { UsuarioCampoDatoModel } from '../models/usuarioCampoDatoModel'
import { UsuarioModel } from '../models/usuarioModel'

export interface UsuarioState {
  usuario: UsuarioModel | undefined
  usuarios: UsuarioCampoDatoModel | undefined
  roles: RoleModel[] | undefined
}

export const ADD_USUARIOS = 'ADD_USUARIOS'
export const ADD_USUARIO = 'ADD_USUARIO'
export const CLEAR_USUARIO = '@todo/CLEAR_USUARIO'
export const ADD_ROLES = 'ADD_ROLES'

interface AddUsuariosRequest {
  type: typeof ADD_USUARIOS
  payload: UsuarioCampoDatoModel | undefined
}
interface AddUsuarioRequest {
  type: typeof ADD_USUARIO
  payload: Partial<UsuarioModel> | undefined
}
interface ClearUsuarioRequest {
  type: typeof CLEAR_USUARIO
  payload: UsuarioModel | undefined
}

interface AddRolesRequest {
  type: typeof ADD_ROLES
  payload: RoleModel[] | undefined
}

export type UsuarioActionsTypes =
  | AddUsuariosRequest
  | AddUsuarioRequest
  | ClearUsuarioRequest
  | AddRolesRequest
