import { useState, ReactElement, useEffect, useRef } from 'react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import ScrollView from 'devextreme-react/scroll-view'
import { SharedGrid } from 'react-library-grid'
import { RootState } from '../../../../redux/combineReducers'
import { addHideLoader } from '../../../../redux/actions/configActions'
import NotFound from '../../../../components/notFound/notFound'
import {
  getMapperSujeto,
  getStructure,
  getSujeto,
} from '../service/sujetoService'
import { addSujeto, addSujetos, clearSujeto } from '../redux/sujetoActions'
import { ButtonColumnModel, DataGridModel } from 'shared-models'
import { Popup } from 'devextreme-react'
import SujetoFormPage from './sujetoFormPage'
import { SujetoModel } from '../models/sujetoModel'
import { addCompanies } from '../../companyPage/redux/companiesActions'
import { getCompanies } from '../../companyPage/services/companiesServices'
import { useHistory } from 'react-router-dom'
import { SujetosCampoDatoModel } from '../models/sujetosCampoDatoModel'

import { formatDate } from 'devextreme/localization'
import { isOpenPopup } from '../../../../redux/actions/sharedUtilsActions'
import { useScreenSize } from '../../../../utils/media-query'

function SujetosPage(): ReactElement {
  const dispatch = useDispatch()
  const [structure, setStructure] = useState(new SujetosCampoDatoModel())
  const userState = useSelector((state: RootState) => state.user)
  const sujetoState = useSelector((state: RootState) => state.sujeto)
  const [rowFocused, setRowFocused] = useState('')
  const [showForm, setShowForm] = useState(false)
  const [rowSelected, setRow] = useState(0)
  const history = useHistory()
  const popupSujetosRef = useRef(null)
  const { isLarge, isMedium, isSmall, isXSmall } = useScreenSize()
  useEffect(() => {
    async function fetchData() {
      try {
        setStructure(await getStructure(userState!.user!))
        await dispatch(addCompanies(await getCompanies(userState!.user!)))
        dispatch(addHideLoader(false))
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [])

  const openCreatePopup = async () => {
    dispatch(addSujeto(new SujetoModel()))
    setShowForm(!showForm)
    dispatch(isOpenPopup(!showForm, popupSujetosRef))
  }
  const dataGridSujetosRef = useRef(null)
  function fillDataGrid(): DataGridModel {
    const buttonColumn: ButtonColumnModel = new ButtonColumnModel({
      icon: 'edit',
      text: '',
      hint: 'Editar',
      cellRender: async (row: any) => {
        setRow(row.data)
        setShowForm(!showForm)
        const sujeto: SujetoModel = getMapperSujeto(
          await getSujeto(userState!.user!, row.data.Id),
        )
        dispatch(addSujeto(sujeto))
      },
    })

    let dataGridModel = new DataGridModel({
      dataGridName: 'sujetoGridComponent',
      ref: '',
      rowFocused: rowFocused,
      columns: structure.Campos,
      openRow: ({}) => {},
      openRowEnterKey: ({}) => {},
      focusEventChanged: (e: any) => {
        if (e.row) {
          setRowFocused(e.row.key)
        }
      },
      focusEventChanging: ({}) => {},
      openCreatePopup: openCreatePopup,
      user: userState!.user!,
      url: `${process.env.REACT_APP_API_URL_CORE}/Sujeto`,
      urlAux: `${process.env.REACT_APP_API_URL_CORE}/Sujeto`,
      buttonsColumns: [buttonColumn],
      filterRow: true,
      filterPanel: true,
      columnChooser: true,
      headerFilter: true,
      exportData: true,
      showSearchPanel: true,
      showToolbar: true,
      history: history,
      singularName: 'Sujeto',
      pluralName: 'Sujetos',
      pageName: 'Sujeto',
      openedPopup: showForm,
    })

    return dataGridModel
  }

  return (
    <>
      {/* {isAllowed(PermisoType.AccesoAdministracion, userState!.user) ? ( */}
      {true ? (
        <div
          className={'content-block dx-card responsive-paddings'}
          style={{ borderColor: '#f4f9ff', borderWidth: '2px', margin: '0.6%' }}
        >
          <h3 style={{ margin: 0, padding: 0 }}>Sujetos</h3>
          {structure?.Campos != undefined && structure?.Campos.length > 0 ? (
            <ScrollView direction="vertical">
              <div style={{ height: '75vh' }}>
                <SharedGrid dataGridModel={fillDataGrid()} />
              </div>
            </ScrollView>
          ) : null}
          <Popup
            visible={showForm}
            ref={popupSujetosRef}
            onHiding={({}) => {
              setShowForm(!showForm)
              dispatch(isOpenPopup(false, popupSujetosRef))
            }}
            dragEnabled={true}
            closeOnOutsideClick={false}
            showTitle={true}
            title="Formulario Sujeto"
            width={'60%'}
            height={'50%'}
          >
            <SujetoFormPage
              row={rowSelected}
              setShowForm={setShowForm}
              showForm={showForm}
            ></SujetoFormPage>
          </Popup>
        </div>
      ) : (
        <div>
          <NotFound data={{ type: 1 }}></NotFound>
        </div>
      )}
      {}
    </>
  )
}

export default SujetosPage
