import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'
import { checkStatusCode, logType, methodsEndPointType } from 'shared-utils'
import { logger } from 'login-service'
import { customLog } from 'shared-utils'
import { authHeader } from '../../../loginPage/service/authService'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { CalculadoraTransferenciaModel } from '../models/calculadoraTransferenciaModel'
import { UserModel } from 'shared-models'

const serviceName = 'CalculadoraService'

export const getCalculadora = async (
  user: UserModel,
  idCompany: Number,
): Promise<CalculadoraTransferenciaModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/CalculadoraTransferencia/GetByCompany/${idCompany}`
    let CalculadoraData: CalculadoraTransferenciaModel =
      new CalculadoraTransferenciaModel()
    await axios({
      url: url,
      method: 'GET',
      headers: authHeader(user!),
    })
      .then((response) => {
        CalculadoraData = jsonConvert.deserializeObject(
          response.data,
          CalculadoraTransferenciaModel,
        )
      })
      .catch((error) => {
        showToast(
          'No se han podido obtener de la calculadora.',
          NotifyType.error,
          5000,
        )
      })

    return CalculadoraData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}

export const postCalculadora = async (
  user: UserModel,
  calculadora: CalculadoraTransferenciaModel | undefined,
  idCompany: Number,
): Promise<CalculadoraTransferenciaModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/CalculadoraTransferencia`
    let calculadoraData: CalculadoraTransferenciaModel =
      new CalculadoraTransferenciaModel()

    await axios({
      url: url,
      method: 'POST',
      headers: authHeader(user!),
      data: {
        IdCompany: idCompany,
        CuotaTurismos: calculadora?.CuotaTurismos,
        CuotaMotocicletas: calculadora?.CuotaMotocicletas,
        Mensaje: calculadora?.CuotaMotocicletas,
      },
    })
      .then((response) => {
        console.log('Este es el Codigo de respuesta' + response.status)
        checkStatusCode(response.status)
        calculadoraData = jsonConvert.deserializeObject(
          response.data,
          CalculadoraTransferenciaModel,
        )
        // showToast(
        //   'Configuración Guardada correctamente!.',
        //   NotifyType.success,
        //   5000,
        // )
      })
      .catch((error) => {
        customLog(
          `Error al crear Calculadora => ${error.response.data}`,
          logType.ERROR,
          serviceName,
          postCalculadora.name,
          methodsEndPointType.POST,
        )
        // showToast(
        //   'No se han podido Guardar la configuración del Email.',
        //   NotifyType.error,
        //   5000,
        // )
      })

    return calculadoraData
  } catch (error: any) {
    showToast(error.response.data, NotifyType.error, 5000)
  }
}
