import {
  SujetoActionsTypes,
  ADD_SUJETO,
  CLEAR_SUJETO,
  SujetoState,
  ADD_SUJETOS,
  SujetosState,
} from './sujetoType'

const initialState: SujetoState = {
  sujeto: undefined,
  sujetos: undefined,
}

export const sujetoReducer = (
  state = initialState,
  action: SujetoActionsTypes,
): SujetoState => {
  switch (action.type) {
    case ADD_SUJETO: {
      return { ...state, sujeto: action.payload }
    }
    case ADD_SUJETOS: {
      return { ...state, sujetos: action.payload }
    }
    case CLEAR_SUJETO: {
      return { ...state, sujeto: action.payload }
    }
    default:
      return state
  }
}
