import { SujetoModel } from '../models/sujetoModel'
import { SujetosCampoDatoModel } from '../models/sujetosCampoDatoModel'
import {
  SujetoActionsTypes,
  ADD_SUJETOS,
  ADD_SUJETO,
  CLEAR_SUJETO,
} from './sujetoType'

export const addSujeto = (
  sujeto: Partial<SujetoModel> | SujetoModel | undefined,
): SujetoActionsTypes => ({
  type: ADD_SUJETO,
  payload: sujeto,
})

export const clearSujeto = (
  sujeto: SujetoModel | undefined,
): SujetoActionsTypes => ({
  type: CLEAR_SUJETO,
  payload: sujeto,
})
export const addSujetos = (
  sujetos: SujetosCampoDatoModel | undefined,
): SujetoActionsTypes => ({
  type: ADD_SUJETOS,
  payload: sujetos,
})
