import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'
import { UserModel } from 'shared-models'
// import { UserModel } from 'shared-models'
import { authHeader } from 'shared-services'
import {
  checkStatusCode,
  customLog,
  logType,
  methodsEndPointType,
} from 'shared-utils'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { EnlaceModel } from '../models/enlaceModel'

const serviceName = 'EnlaceService'
export const getEnlaces = async (
  user: UserModel,
  idCompany: Number,
): Promise<EnlaceModel[] | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/Enlace/GetByCompany/${idCompany}`
    let enlacesData: EnlaceModel[] = []
    await axios({
      url: url,
      method: 'GET',
      headers: authHeader(user!),
    })
      .then((response) => {
        checkStatusCode(response.status)
        enlacesData = jsonConvert.deserializeArray(response.data, EnlaceModel)
      })
      .catch((error) => {
        showToast(
          'No se han podido obtener la configuración Legal. => ' + error,
          NotifyType.error,
          5000,
        )
      })

    return enlacesData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}

export const postEnlaces = async (
  user: UserModel,
  enlaces: EnlaceModel[] | undefined,
  idCompany: number,
): Promise<Boolean> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/Enlace`

    const headers = {
      Authorization: 'Bearer my-token',
      'My-Custom-Header': 'foobar',
    }
    enlaces = fillCompany(enlaces!, idCompany)
    await axios.post(url, enlaces, { headers }).then(async (response) => {
      checkStatusCode(response.status)
      customLog(
        `enlaces creados ${response.data.Id}`,
        logType.INFO,
        serviceName,
        postEnlaces.name,
        methodsEndPointType.POST,
      )
      // showToast('Enlaces creados', NotifyType.success, 5000)
    })
    return true
  } catch (error) {
    // showToast(error.response.data, NotifyType.error, 5000)
    customLog(
      `Error al crear enlaces => ${error.response.data}`,
      logType.ERROR,
      serviceName,
      postEnlaces.name,
      methodsEndPointType.POST,
    )
    return false
  }
}

export const putEnlaces = async (
  user: UserModel,
  enlaces: EnlaceModel[] | undefined,
  idCompany: number,
): Promise<Boolean> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/Enlace`

    const headers = {
      Authorization: 'Bearer my-token',
      'My-Custom-Header': 'foobar',
    }
    enlaces = fillCompany(enlaces!, idCompany)
    await axios
      .put(url, enlaces, { headers })
      .then((response) => {
        checkStatusCode(response.status)
        customLog(
          `Enlaces modificados`,
          logType.INFO,
          serviceName,
          putEnlaces.name,
          methodsEndPointType.PUT,
        )
        showToast('Enlaces modificados', NotifyType.success, 5000)
      })
      .catch((error) => {
        customLog(
          `Error al editar enlaces`,
          logType.ERROR,
          serviceName,
          putEnlaces.name,
          methodsEndPointType.PUT,
        )
      })
    return true
  } catch (error) {
    return false
  }
}

const fillCompany = (
  enlaces: EnlaceModel[],
  idCompany: number,
): EnlaceModel[] => {
  for (const enlace of enlaces) {
    enlace.IdCompany = idCompany
  }
  return enlaces
}
