import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Button, Form, LoadIndicator } from 'devextreme-react'
import { GroupItem, RequiredRule, SimpleItem } from 'devextreme-react/form'
import { confirm } from 'devextreme/ui/dialog'
import { useForm } from 'react-hook-form'
import { RootState } from '../../../../redux/combineReducers'
import { useScreenSize } from '../../../../utils/media-query'
import { postSujeto, putSujeto } from '../service/sujetoService'

import { addSujeto } from '../redux/sujetoActions'
import { NIFValidator } from '../../../../utils/Validators/validators'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'
import { warningFormPrevent } from '../../../../utils/sharedUitls'

function SujetoFormPage(props: any): ReactElement {
  const userState = useSelector((state: RootState) => state.user)
  const companyState = useSelector((state: RootState) => state.company)
  const sujetoState = useSelector((state: RootState) => state.sujeto.sujeto)
  const { isXSmall, isLarge } = useScreenSize()
  const [loading, setLoading] = useState(false)
  const formData = useRef({})
  const { register, handleSubmit } = useForm()
  const dispatch = useDispatch()
  useEffect(() => {
    async function fetchData() {}
    fetchData()
  }, [])

  const onSubmit = async (data: any) => {
    setLoading(true)

    let confirmDialog = confirm(
      '<i>¿Está seguro de que quiere guardar la información?</i>',
      'Confirma los cambios',
    )
    confirmDialog.then((dialogResult) => {
      if (dialogResult) {
        dialogCall()
      } else {
        setLoading(false)
      }
    })

    const dialogCall = async () => {
      let result: boolean = false
      if (!sujetoState?.Id) {
        dispatch(addSujeto({ ...sujetoState, Id: 0 }))
      }

      if (sujetoState?.Id) {
        result = await putSujeto(userState!.user!, sujetoState)
      } else {
        result = await postSujeto(userState!.user!, sujetoState!)
      }

      if (result) {
        setLoading(false)
        const refreshButton = document.getElementsByClassName(
          'dx-icon dx-icon-refresh',
        )[0] as HTMLElement
        refreshButton.click()
        props.setShowForm(false)
      } else {
        setLoading(false)
      }
    }
  }

  const handleCancelClick = () => {
    props.setShowForm(false)
    // warningFormPrevent(false,formSujetoRef)
  }

  const renderButtons = () => {
    return renderButtonsComponent(`sujetoData`, handleCancelClick)
  }

  const selectedBoxOptions = {
    items: companyState?.companies?.Datos,
    value: sujetoState?.IdCompany,
    placeholder: 'Seleccione una compañía',
    displayExpr: 'Name',
    valueExpr: 'Id',
    onValueChanged: (event: any) => {
      if (event.event) {
        handleUpdatedSujeto({ IdCompany: event.value })
      }
    },
  }

  const handleUpdatedSujeto = (property: any) => {
    dispatch(
      addSujeto({
        ...sujetoState,
        ...property,
      }),
    )
  }
  //const formSujetoRef=useRef(null);
  return (
    <>
      <div className={'content-block  responsive-paddings'}>
        <div className={'content-block  '}>
          <form
            className={'legalForm'}
            onSubmit={handleSubmit(onSubmit)}
            encType="multipart/form-data"
            style={{ width: '100%' }}
          >
            <Form
              formData={formData.current}
              disabled={loading}
              //ref={formSujetoRef}
              readOnly={false}
              labelLocation={isLarge ? 'left' : 'top'}
              showValidationSummary={false}
              validationGroup="sujetoData"
              showColonAfterLabel={true}
            >
              <GroupItem colCount="2">
                <SimpleItem
                  dataField="Nif"
                  helpText="Formato Valido Ex:77777777D"
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'NIF',
                    mode: 'text',
                    value: sujetoState?.NIF ? sujetoState : null,
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedSujeto({ NIF: event.value })
                      }
                    },
                    ...NIFValidator,
                  }}
                >
                  <RequiredRule message="Debes introducir un NIF" />
                </SimpleItem>
                <SimpleItem
                  dataField={'Slogan'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Nombre',
                    mode: 'text',
                    value: sujetoState?.Nombre ? sujetoState.Nombre : null,
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedSujeto({ Nombre: event.value })
                      }
                    },
                  }}
                >
                  <RequiredRule message="Debes introducir un Nombre" />
                </SimpleItem>
                <SimpleItem
                  dataField="Compañia"
                  editorType="dxSelectBox"
                  editorOptions={selectedBoxOptions}
                >
                  <RequiredRule message="Debes elegir al menos una Compañía" />
                </SimpleItem>
              </GroupItem>
              <SimpleItem render={renderButtons} cssClass="alignBottom" />
            </Form>
          </form>
        </div>
      </div>
    </>
  )
}

export default SujetoFormPage
