import {
  ADD_CALCULADORA_TRANSFERENCIAS,
  CalculadoraTransferenciasActionsTypes,
  CalculadoraTransferenciasState,
  CLEAR_CALCULADORA_TRANSFERENCIAS,
} from './calculadoraTransferenciasType'

const initialState: CalculadoraTransferenciasState = {
  calculadoraTransferencias: undefined,
}

export const calculadoraTransferenciasReducer = (
  state = initialState,
  action: CalculadoraTransferenciasActionsTypes,
): CalculadoraTransferenciasState | undefined => {
  switch (action.type) {
    case ADD_CALCULADORA_TRANSFERENCIAS: {
      return { ...state, calculadoraTransferencias: action.payload }
    }
    case CLEAR_CALCULADORA_TRANSFERENCIAS: {
      return { ...state, calculadoraTransferencias: action.payload }
    }
    default:
      return state
  }
}
