import { JsonObject, JsonProperty } from 'json2typescript'

@JsonObject('ComponenteModel')
export class ComponenteModel {
  @JsonProperty('Id', Number)
  Id: Number = 0
  @JsonProperty('IdPlantilla', Number)
  IdPlantilla: Number = 0
  @JsonProperty('IdTipoComponente', Number)
  IdTipoComponente: Number = 0
  @JsonProperty('TipoComponente', String || null)
  TipoComponente: String = ''
  @JsonProperty('Plantilla', String || null)
  Plantilla: String = ''
  @JsonProperty('ComponentesAsociados', String || null)
  ComponentesAsociados: String = ''
}
