import { JsonObject, JsonProperty } from 'json2typescript'

@JsonObject('ModuloComponenteModel')
export class ModuloComponenteModel {
  @JsonProperty('Id', Number, 0)
  Id: Number = 0
  @JsonProperty('IdPlantilla', Number, 0)
  IdPlantilla: Number = 0
  @JsonProperty('IdTipoComponente', Number, 0)
  IdTipoComponente: Number = 0
  @JsonProperty('Nombre', String)
  Nombre: string = ''
}
