import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { Form } from 'devextreme-react'
import {
  Item,
  Label,
  NumericRule,
  RequiredRule,
  SimpleItem,
} from 'devextreme-react/form'
import { checkScreenSize } from 'shared-utils'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../../../redux/combineReducers'
import { addCalculadoraTranseferencias } from '../redux/CalculadoraTransferencias/calculadoraTransferenciasActions'
import { CalculadoraTransferenciaModel } from '../models/calculadoraTransferenciaModel'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'
import { useForm } from 'react-hook-form'
import { addCompany } from '../redux/companiesActions'
import { postCompany, putCompany } from '../services/companiesServices'
import { addDataFormChange } from '../../../../redux/actions/sharedUtilsActions'
import { refreshGrid } from '../../../../utils/sharedUitls'
import { addParentEnlaces } from '../redux/Enlace/enlaceActions'
import { getEnlaces } from '../services/enlacesService'

function CalculadoraFormComponent(props: any): ReactElement {
  const calculadoraTransferenciasState = useSelector(
    (state: RootState) =>
      state.calculadoraTransferencias?.calculadoraTransferencias,
  )
  const userState = useSelector((state: RootState) => state.user)
  const company = useSelector((state: RootState) => state.company?.company)
  const companyState = useSelector((state: RootState) => state.company)
  const emailState = useSelector((state: RootState) => state.email?.email)
  const enlaceState = useSelector((state: RootState) => state.enlace)
  const isChangedState = useSelector(
    (state: RootState) => state.sharedUtils.isChanged,
  )
  const [loading, setLoading] = useState(false)
  const formData = useRef({})
  const dispatch = useDispatch()
  const { register, handleSubmit } = useForm()
  useEffect(() => {
    async function fetchData() {
      try {
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [props.showForm])
  const onSubmit = async (data: any) => {
    setLoading(true)
    let result: boolean = false
    if (!company?.Id) {
      dispatch(addCompany({ ...company, Id: 0 }))
    }
    if (company?.Id) {
      result = await putCompany(
        userState!.user!,
        company,
        calculadoraTransferenciasState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    } else {
      result = await postCompany(
        userState!.user!,
        company!,
        calculadoraTransferenciasState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    }

    if (result) {
      setLoading(false)
      refreshGrid(props, true)
      props.setShowForm(false)
      localStorage.setItem(
        'parentCompanyModulos',
        JSON.stringify(companyState?.companyModulos),
      )
      localStorage.setItem(
        'parentEnlaces',
        JSON.stringify(
          dispatch(
            addParentEnlaces(
              await getEnlaces(
                userState?.user!,
                parseInt(userState?.user!.CompanyId!),
              ),
            ),
          ).payload,
        ),
      )
    } else {
      setLoading(false)
    }
  }

  const handleUpdatedCalculadora = (property: any) => {
    dispatch(
      addCalculadoraTranseferencias({
        ...calculadoraTransferenciasState,
        ...property,
      }),
    )
  }
  const handleCancelClick = () => {
    dispatch(addCalculadoraTranseferencias(new CalculadoraTransferenciaModel()))
    props.setShowForm(false)
    /*  calculadoraFormRef.current.instance.resetValues() */
  }
  const renderButtons = () => {
    if (!props.showForm) {
      calculadoraFormRef.current.instance.resetValues()
    }
    return renderButtonsComponent(`calculadoraData`, handleCancelClick)
  }

  const calculadoraFormRef: any = useRef(null)

  if (!props.showForm) {
    if (calculadoraFormRef != null) {
      calculadoraFormRef.current?.instance.resetValues()
    }
  }
  return (
    <>
      {' '}
      <div
        className={'content-block responsive-paddings'}
        style={{ borderColor: '#f4f9ff', borderWidth: '2px', margin: '0.6%' }}
      >
        <div className="form">
          <form
            className={'calculadoraForm'}
            onSubmit={handleSubmit(onSubmit)}
            style={{ width: '100%' }}
          >
            <Form
              formData={formData.current}
              ref={calculadoraFormRef}
              disabled={loading}
              labelLocation={checkScreenSize() ? 'left' : 'top'}
              validationGroup="calculadoraData"
            >
              <Item
                dataField={'CuotaTurismos'}
                editorType={'dxNumberBox'}
                editorOptions={{
                  stylingMode: 'filled',
                  format: {
                    style: 'currency',
                    currency: 'EUR',
                    useGrouping: true,
                  },
                  placeholder:
                    'Cuota de turismos/todoterrenos/comerciales (Ej. 21,60)',
                  value: calculadoraTransferenciasState?.CuotaTurismos
                    ? calculadoraTransferenciasState?.CuotaTurismos
                    : null,
                  onValueChanged: (event: any) => {
                    if (event.event) {
                      handleUpdatedCalculadora({ CuotaTurismos: event.value })
                      if (!isChangedState) dispatch(addDataFormChange(true))
                    }
                  },
                }}
              >
                <RequiredRule message="El campo es requerido" />
                <NumericRule message="El Campo debe ser numérico" />
                <Label visible={true} text="Cuota Coches" />
              </Item>

              <Item
                dataField={'CuotaMotocicletas'}
                editorType={'dxNumberBox'}
                editorOptions={{
                  stylingMode: 'filled',
                  format: {
                    style: 'currency',
                    currency: 'EUR',
                    useGrouping: true,
                  },
                  placeholder:
                    'Cuota de motocicletas/ciclomotores/quads (Ej. 21,60)',
                  value: calculadoraTransferenciasState?.CuotaTurismos
                    ? calculadoraTransferenciasState.CuotaMotocicletas
                    : null,
                  onValueChanged: (event: any) => {
                    if (event.event) {
                      handleUpdatedCalculadora({
                        CuotaMotocicletas: event.value,
                      })
                      if (!isChangedState) dispatch(addDataFormChange(true))
                    }
                  },
                }}
              >
                <RequiredRule message="El campo es requerido" />
                <NumericRule message="El Campo debe ser numérico" />
                <Label visible={true} />
              </Item>
              <Item
                dataField={'Mensaje'}
                editorType={'dxTextBox'}
                editorOptions={{
                  stylingMode: 'filled',
                  placeholder: 'Mensaje de responsabilidad legal',
                  value: calculadoraTransferenciasState?.Mensaje
                    ? calculadoraTransferenciasState?.Mensaje
                    : '',
                  onValueChanged: (event: any) => {
                    if (event.event) {
                      handleUpdatedCalculadora({ Mensaje: event.value })
                      if (!isChangedState) dispatch(addDataFormChange(true))
                    }
                  },
                }}
              >
                <Label visible={true} text="Mensaje Legal" />
              </Item>
            </Form>
          </form>
        </div>
      </div>
    </>
  )
}
export default CalculadoraFormComponent
