const serviceName = 'UsuarioService'

export const getPlantillas = async (
  user: UserModel,
): Promise<PlantillaCampoDatoModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/plantillas`

    let plantillas: PlantillaCampoDatoModel = new PlantillaCampoDatoModel()
    let datos
    await axios({
      url: url,
      method: methodsEndPointType.GET,
      headers: authHeader(user!),
    })
      .then((response) => {
        plantillas = jsonConvert.deserializeObject(
          response.data,
          PlantillaCampoDatoModel,
        )
        datos = response.data
      })
      .catch((error) => {
        customLog(
          null,
          logType.ERROR,
          serviceName,
          getPlantillas.name,
          methodsEndPointType.GET,
        )
        showToast(
          'No se han podido obtener las plantillas.',
          NotifyType.error,
          5000,
        )
      })

    return plantillas
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
