export async function getPlantillaWithChild(
  user: UserModel,
  id: number,
): Promise<PlantillaModel[]> {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let url = `${process.env.REACT_APP_API_URL_CORE}/Plantilla/PlantillaWithChild/${id}`

  let plantillas: PlantillaModel[] = []
  await axios({
    url: url,
    method: methodsEndPointType.GET,
    headers: authHeader(user),
  })
    .then((response) => {
      checkStatusCode(response.status)
      plantillas = jsonConvert.deserializeArray(
        response.data.Datos,
        PlantillaModel,
      )
    })
    .catch((error) => {
      customLog(
        null,
        logType.ERROR,
        serviceName,
        getPlantillaWithChild.name,
        methodsEndPointType.GET,
      )
      showToast(
        'No se han podido plantillas relacionadas',
        NotifyType.error,
        5000,
      )
    })

  return plantillas
}
