export async function postPlantilla(
  user: UserModel,
  plantillaCampos: PlantillaCamposModel[],
  extraData: any,
): Promise<boolean> {
  let created = false
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/PlantillaCampo`
    let orden = 0
    plantillaCampos.map((plantillaCampo) => {
      orden++
      return (plantillaCampo.Orden = orden)
    })
    const plantillaCamposBody = {
      PlantillaCampos: [...plantillaCampos],
      ...extraData,
    }

    await axios
      .post(url, plantillaCamposBody, { headers: authHeader(user) })
      .then((response) => {
        checkStatusCode(response.status)
        // plantillaCampos = jsonConvert.deserializeArray(
        //   response.data,
        //   PlantillaCamposModel,
        // )
        customLog(
          `Plantilla created ${response.data.Id}`,
          logType.INFO,
          serviceName,
          postPlantilla.name,
          methodsEndPointType.POST,
        )
        showToast('Plantilla creada correctamente', NotifyType.success, 5000)
        created = true
      })
      .catch((error) => {
        showToast(error.response.data.Message, NotifyType.error, 5000)
        customLog(
          `Error create plantilla  => ${error.message}`,
          logType.ERROR,
          serviceName,
          postPlantilla.name,
          methodsEndPointType.PUT,
        )
        checkStatusCode(error.request.status)
        created = false
      })
  } catch (error:any) {
    showToast(error.response.data, NotifyType.error, 5000)
    customLog(
      `Error created plantilla => ${error.message}`,
      logType.ERROR,
      serviceName,
      postPlantilla.name,
      methodsEndPointType.POST,
    )
    showToast(error.response.data, NotifyType.error, 5000)
  }
  return created
}
export async function putPlantilla(
  user: UserModel,
  plantillaCampos: PlantillaCamposModel[],
  extraData: any,
): Promise<boolean> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/PlantillaCampo`

    let orden = 0
    plantillaCampos.map((plantillaCampo) => {
      orden++
      return (plantillaCampo.Orden = orden)
    })
    const plantillaCamposBody = {
      PlantillaCampos: [...plantillaCampos],
      ...extraData,
    }

    await axios
      .put(url, plantillaCamposBody, { headers: authHeader(user) })
      .then((response) => {
        checkStatusCode(response.status)
        customLog(
          `plantilla modificada ${plantillaCamposBody.Id}`,
          logType.INFO,
          serviceName,
          putPlantilla.name,
          methodsEndPointType.PUT,
        )
        showToast('Plantilla editada', NotifyType.success, 5000)
      })
      .catch((error) => {
        customLog(
          `Error al editar Plantilla => ${error.message}`,
          logType.ERROR,
          serviceName,
          putPlantilla.name,
          methodsEndPointType.PUT,
        )
      })
    return true
  } catch (error) {
    return false
  }
}

export async function deletePlantilla(user: UserModel, idPlantilla: number) {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/Plantilla/${idPlantilla}`

    await axios.delete(url, { headers: authHeader(user!) }).then((response) => {
      checkStatusCode(response.status)
      customLog(
        `plantilla elminada`,
        logType.INFO,
        serviceName,
        deletePlantilla.name,
        methodsEndPointType.POST,
      )
      showToast('Plantilla elminada', NotifyType.success, 5000)
    })
    return true
  } catch (error:any) {
    if (error.response.status == 400) {
      showToast(error.response.data, NotifyType.error, 5000)
    }
    customLog(
      `Error al eliminar Plantilla => ${error.response.data}`,
      logType.ERROR,
      serviceName,
      deletePlantilla.name,
      methodsEndPointType.POST,
    )
    checkStatusCode(error.request.status)
    return false
  }
}

export function getMapperPlantilla(usuarioData: any): PlantillaModel {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let plantilla: PlantillaModel = jsonConvert.deserializeObject(
    usuarioData,
    PlantillaModel,
  )
  return plantilla
}
