import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'

import {
  logType,
  methodsEndPointType,
  customLog,
  checkStatusCode,
} from 'shared-utils'
import { ForgotPasswordModel, UserModel } from 'shared-models'

import { NotifyType, showToast } from '../../../../utils/sharedUitls'

import { PerfilesCampoDatoModel } from '../models/perfilCampoDatoModel'
import { PerfilModel } from '../models/perfilModel'
import { authHeader } from 'shared-services'

const serviceName = 'authService'

export const getPerfiles = async (
  user: UserModel,
): Promise<PerfilModel[] | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/perfil`
    let PerfilesData: PerfilModel[] = []
    let datos
    await axios({
      url: url,
      method: 'GET',
      headers: authHeader(user),
    })
      .then((response) => {
        PerfilesData = jsonConvert.deserializeArray(response.data, PerfilModel)
      })
      .catch((error) => {
        customLog(
          null,
          logType.ERROR,
          serviceName,
          getPerfiles.name,
          methodsEndPointType.GET,
        )
        showToast(
          'No se han podido obtener los perfiles.',
          NotifyType.error,
          5000,
        )
      })

    return PerfilesData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
export async function getPerfil(
  user: UserModel,
  id: number,
): Promise<PerfilModel> {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let url = `${process.env.REACT_APP_API_URL_CORE}/Perfil/${id}`

  let perfil: PerfilModel = new PerfilModel()
  await axios({
    url: url,
    method: methodsEndPointType.GET,
    headers: authHeader(user),
  })
    .then((response) => {
      checkStatusCode(response.status)
      perfil = jsonConvert.deserializeObject(response.data, PerfilModel)
    })
    .catch((error) => {
      customLog(
        null,
        logType.ERROR,
        serviceName,
        getPerfil.name,
        methodsEndPointType.GET,
      )
      showToast('No se han podido obtener el perfil.', NotifyType.error, 5000)
    })

  return perfil
}
