export async function putPerfil(
  user: UserModel,
  perfil: PerfilModel,
): Promise<boolean> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/Perfil`
    await axios
      .put(url, perfil, { headers: authHeader(user) })
      .then((response) => {
        checkStatusCode(response.status)
        customLog(
          `perfil modificado ${perfil.Id}`,
          logType.INFO,
          serviceName,
          putPerfil.name,
          methodsEndPointType.PUT,
        )
        showToast('Perfil editado', NotifyType.success, 5000)
      })
      .catch((error) => {
        customLog(
          `Error al editar Perfil => ${error.message}`,
          logType.ERROR,
          serviceName,
          putPerfil.name,
          methodsEndPointType.PUT,
        )
      })
    return true
  } catch (error) {
    return false
  }
}
export async function getStructure(
  user: UserModel,
): Promise<PerfilesCampoDatoModel> {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let url = `${process.env.REACT_APP_API_URL_CORE}/Perfil/Structure`

  let structure: PerfilesCampoDatoModel = new PerfilesCampoDatoModel()
  await axios({
    url: url,
    method: methodsEndPointType.GET,
    headers: authHeader(user),
  })
    .then((response) => {
      checkStatusCode(response.status)
      structure = jsonConvert.deserializeObject(
        response.data,
        PerfilesCampoDatoModel,
      )
    })
    .catch((error: any) => {
      customLog(
        null,
        logType.ERROR,
        serviceName,
        getStructure.name,
        methodsEndPointType.GET,
      )
      return {}
    })

  return structure
}

export function getMapperSujeto(perfilData: any): PerfilModel {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let perfil: PerfilModel = jsonConvert.deserializeObject(
    perfilData,
    PerfilModel,
  )
  return perfil
}
