import { ConfiguracionEnvioEmailModel } from '../../models/configuracionEnvioEmailModel'
import { EnlaceModel } from '../../models/enlaceModel'
import { EnlacesModel } from '../../models/enlacesModel'

import {
  ADD_ENLACE,
  ADD_ENLACES,
  ADD_PARENT_ENLACES,
  CLEAR_ENLACE,
  EnlaceActionsTypes,
} from './enlaceType'

export const addEnlace = (
  enlace: Partial<EnlaceModel> | EnlaceModel | undefined,
): EnlaceActionsTypes => ({
  type: ADD_ENLACE,
  payload: enlace,
})
export const addParentEnlaces = (
  parentEnlaces: Partial<EnlaceModel[]> | EnlacesModel[] | undefined,
): EnlaceActionsTypes => ({
  type: ADD_PARENT_ENLACES,
  payload: parentEnlaces,
})
export const addEnlaces = (
  enlaces: Partial<EnlaceModel[]> | EnlacesModel[] | undefined,
): EnlaceActionsTypes => ({
  type: ADD_ENLACES,
  payload: enlaces,
})

export const clearEnlace = (
  enlace: EnlaceModel | undefined,
): EnlaceActionsTypes => ({
  type: CLEAR_ENLACE,
  payload: enlace,
})
