import {
  ADD_DATOS_USUARIOS,
  ADD_DATO_USUARIO,
  CLEAR_DATO_USUARIO,
  DatoUsuarioActionsTypes,
  DatoUsuarioState,
} from './datoUsuarioType'

const initialState: DatoUsuarioState = {
  datoUsuario: undefined,
  datosUsuarios: undefined,
}

export const datoUsuarioReducer = (
  state = initialState,
  action: DatoUsuarioActionsTypes,
): DatoUsuarioState | undefined => {
  switch (action.type) {
    case ADD_DATOS_USUARIOS: {
      return { ...state, datosUsuarios: action.payload }
    }
    case ADD_DATO_USUARIO: {
      return { ...state, datoUsuario: action.payload }
    }
    case CLEAR_DATO_USUARIO: {
      return { ...state, datoUsuario: action.payload }
    }
    default:
      return state
  }
}
