import {
  ADD_ENLACE,
  ADD_ENLACES,
  ADD_PARENT_ENLACES,
  CLEAR_ENLACE,
  EnlaceActionsTypes,
  EnlaceState,
} from './enlaceType'

const initialState: EnlaceState = {
  enlace: undefined,
  enlaces: undefined,
  parentEnlaces: undefined,
}

export const enlaceReducer = (
  state = initialState,
  action: EnlaceActionsTypes,
): EnlaceState | undefined => {
  switch (action.type) {
    case ADD_ENLACE: {
      return { ...state, enlace: action.payload }
    }
    case ADD_ENLACES: {
      return { ...state, enlaces: action.payload }
    }
    case ADD_PARENT_ENLACES: {
      return { ...state, parentEnlaces: action.payload }
    }
    case CLEAR_ENLACE: {
      return { ...state, enlace: action.payload }
    }
    default:
      return state
  }
}
