const serviceName = 'CompaniesService'

export const getCompanies = async (
  user: UserModel,
): Promise<CompanyCampoDatoModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/companies`

    let companies: CompanyCampoDatoModel = new CompanyCampoDatoModel()

    const config = {
      headers: { Authorization: `Bearer ${user.Token}` },
    }
    await axios({
      url: url,
      method: 'GET',
      headers: authHeader(user!),
    })
      .then((response) => {
        companies = jsonConvert.deserializeObject(
          response.data,
          CompanyCampoDatoModel,
        )
      })
      .catch((error) => {
        customLog(
          null,
          logType.ERROR,
          serviceName,
          getCompanies.name,
          methodsEndPointType.GET,
        )
        showToast(
          'No se han podido obtener las compañias.',
          NotifyType.error,
          5000,
        )
        checkStatusCode(error.request.status)
      })

    return companies
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
export async function getCompany(
  user: UserModel,
  id: number,
): Promise<CompanyModel> {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let url = `${process.env.REACT_APP_API_URL_CORE}/Companies/${id}`

  let company: CompanyModel = new CompanyModel()
  await axios({
    url: url,
    method: methodsEndPointType.GET,
    headers: authHeader(user!),
  })
    .then((response) => {
      checkStatusCode(response.status)
      company = jsonConvert.deserializeObject(response.data, CompanyModel)
      company.Enabled = company.Enabled ? false : true
    })
    .catch((error) => {
      customLog(
        null,
        logType.ERROR,
        serviceName,
        getCompany.name,
        methodsEndPointType.GET,
      )
      showToast('No se ha podido obtener las compañia.', NotifyType.error, 5000)
      checkStatusCode(error.request.status)
    })

  return company
}
export async function postCompany(
  user: UserModel,
  company: CompanyModel,
  calculadoraState: CalculadoraTransferenciaModel | undefined,
  enlaceState: EnlaceModel[] | undefined,
  companyState: CompanyState | undefined,
  emailState: ConfiguracionEnvioEmailModel | undefined,
): Promise<boolean> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/companies`
    company.Enabled = company.Enabled ? false : true
    await axios
      .post(url, company, { headers: authHeader(user!) })
      .then(async (response) => {
        checkStatusCode(response.status)
        customLog(
          `compañia creada ${response.data.Id}`,
          logType.INFO,
          serviceName,
          putSujeto.name,
          methodsEndPointType.POST,
        )
        await postCalculadora(user, calculadoraState, response.data.Id)
        await postEnlaces(user, enlaceState, response.data.Id)
        await postEmail(user, emailState, response.data.Id)
        if (isSuperAdministrador(user)) {
          await postCompanyModulo(
            user,
            companyState?.companyModulos,
            response.data.Id,
          )
        }

        showToast('Compañia creada', NotifyType.success, 5000)
      })
    return true
  } catch (error: any) {
    showToast(error.response.data, NotifyType.error, 5000)
    customLog(
      `Error al crear Compañia => ${error.response.data}`,
      logType.ERROR,
      serviceName,
      postCompany.name,
      methodsEndPointType.POST,
    )
    checkStatusCode(error.request.status)
    return false
  }
}
export async function putCompany(
  user: UserModel,
  company: CompanyModel,
  calculadoraState: CalculadoraTransferenciaModel | undefined,
  enlaceState: EnlaceModel[] | undefined,
  companyState: CompanyState | undefined,
  emailState: ConfiguracionEnvioEmailModel | undefined,
): Promise<boolean> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/Companies`
    company.Enabled = company.Enabled ? false : true
    await axios
      .put(url, company, { headers: authHeader(user!) })
      .then(async (response) => {
        checkStatusCode(response.status)
        if (calculadoraState?.Id != 0) {
          await putCalculadora(user, calculadoraState, company.Id)
        } else {
          await postCalculadora(user, calculadoraState, company.Id)
        }
        if (emailState?.Id != 0 && emailState?.Id != undefined) {
          await putEmail(user, emailState, company.Id)
        } else {
          await postEmail(user, emailState, company.Id)
        }

        if (isSuperAdministrador(user)) {
          await postCompanyModulo(
            user,
            companyState?.companyModulos != undefined
              ? companyState.companyModulos
              : [],
            company.Id,
          )
        }
        if (isSuperAdministrador(user)) {
          await putEnlaces(user, enlaceState, company.Id)
        }
        customLog(
          `Compañia modificada ${company.Id}`,
          logType.INFO,
          serviceName,
          putCompany.name,
          methodsEndPointType.PUT,
        )

        showToast('Compañia editada', NotifyType.success, 5000)
      })
      .catch((error) => {
        customLog(
          `Error al editar Compañia => ${error.message}`,
          logType.ERROR,
          serviceName,
          putCompany.name,
          methodsEndPointType.PUT,
        )
        checkStatusCode(error.request.status)
      })
    return true
  } catch (error) {
    return false
  }
}
export async function deleteCompany(user: UserModel, idCompany: number) {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/companies/${idCompany}`

    await axios.delete(url, { headers: authHeader(user!) }).then((response) => {
      checkStatusCode(response.status)
      customLog(
        `compañia elminada`,
        logType.INFO,
        serviceName,
        putSujeto.name,
        methodsEndPointType.POST,
      )
      showToast('Compañia elminada', NotifyType.success, 5000)
    })
    return true
  } catch (error: any) {
    if (error.response.status == 400) {
      showToast(error.response.data, NotifyType.error, 5000)
    }
    customLog(
      `Error al crear Compañia => ${error.response.data}`,
      logType.ERROR,
      serviceName,
      postCompany.name,
      methodsEndPointType.POST,
    )
    checkStatusCode(error.request.status)
    return false
  }
}
