export async function getCompanyModulos(
  user: UserModel,
  idCompany: number,
): Promise<CompanyModuloModel[] | undefined> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    /*     let url = `${process.env.REACT_AP∫P_API_URL}/Administracion/EmailConfig/`; */
    let url = `${process.env.REACT_APP_API_URL_CORE}/CompanyModulo/${idCompany}`
    let companyModulo: CompanyModuloModel[] = []
    await axios({
      url: url,
      method: 'GET',
      headers: authHeader(user!),
    })
      .then((response) => {
        companyModulo = jsonConvert.deserializeArray(
          response.data,
          CompanyModuloModel,
        )
      })
      .catch((error) => {
        showToast(
          'No se han podido obtener los modulos de la compañia.',
          NotifyType.error,
          5000,
        )
        checkStatusCode(error.request.status)
        return []
      })

    return companyModulo
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
export async function postCompanyModulo(
  user: UserModel,
  companyModulos: CompanyModuloModel[] | undefined,
  idCompany: number,
): Promise<boolean> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/CompanyModulo`
    if (companyModulos?.length! > 0) {
      companyModulos?.map((x) => {
        x.IdCompany = idCompany
      })
    }
    await axios
      .post(url, companyModulos, { headers: authHeader(user!) })
      .then(async (response) => {
        checkStatusCode(response.status)
        customLog(
          `compañia creada ${response.data.Id}`,
          logType.INFO,
          serviceName,
          postCompanyModulo.name,
          methodsEndPointType.POST,
        )
      })
    return true
  } catch (error: any) {
    // showToast(error.response.data, NotifyType.error, 5000)
    customLog(
      `Error al crear modulos y sus gestorias => ${error.response.data}`,
      logType.ERROR,
      serviceName,
      postCompany.name,
      methodsEndPointType.POST,
    )
    checkStatusCode(error.request.status)
    return false
  }
}

export function getMapperCompany(companyData: any): CompanyModel {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let company: CompanyModel = jsonConvert.deserializeObject(
    companyData,
    CompanyModel,
  )
  return company
}
