import { PerfilModel } from '../models/perfilModel'
import { PerfilesCampoDatoModel } from '../models/perfilCampoDatoModel'

export interface PerfilState {
  perfil: PerfilModel | undefined
  perfiles: PerfilModel[] | undefined
}

export const ADD_PERFILES = '@todo/ADD_PERFILES'
export const ADD_PERFIL = '@todo/ADD_PERFIL'
export const CLEAR_PERFIL = '@todo/CLEAR_PERFIL'

interface AddPerfilRequest {
  type: typeof ADD_PERFIL
  payload: Partial<PerfilModel> | PerfilModel | undefined
}
interface AddPerfilesRequest {
  type: typeof ADD_PERFILES
  payload: PerfilModel | undefined
}
interface ClearPerfilRequest {
  type: typeof CLEAR_PERFIL
  payload: PerfilModel | undefined
}

export type SujetoActionsTypes =
  | ClearPerfilRequest
  | AddPerfilesRequest
  | AddPerfilRequest
