import { useState, ReactElement, useEffect, useRef } from 'react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../../../redux/combineReducers'
import NotFound from '../../../../components/notFound/notFound'
import { NumberBox, ScrollView, Sortable, Switch } from 'devextreme-react'
import { campos } from '../utils/data'
import './plantillaCamposComponent.scss'
import { addPlantillaCampos } from '../redux/plantilla/plantillaComponenteActions'
import { PlantillaCamposModel } from '../models/plantillaCamposModel/plantillaCamposModel'
function PlantillaCampos(props: any): ReactElement {
  const dispatch = useDispatch()
  const userState = useSelector((state: RootState) => state.user)
  const plantillaState = useSelector((state: RootState) => state.plantilla)
  const [loading, setLoading] = useState(false)
  const [camposState, setCampos] = useState([{}])
  useEffect(() => {
    async function fetchData() {
      try {
        setCampos(campos)
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [])

  const handleUpdatedPlantilla = (id: number, property: any) => {
    let plantillasCampo: PlantillaCamposModel[] = []
    plantillaState.plantillaCampos?.map((plantilla: PlantillaCamposModel) => {
      if (plantilla.IdCampo == id) {
        plantilla = {
          ...plantilla,
          ...property,
        }
      }
      return plantillasCampo.push(plantilla)
    })

    dispatch(addPlantillaCampos(plantillasCampo))
  }

  const onSubmit = async (data: any) => {
    setLoading(true)
    const dialogCall = async () => {
      let result: boolean = false

      if (result) {
        setLoading(false)
      } else {
        setLoading(false)
      }
    }
  }
  const handleCancelClick = () => {
    props.setShowForm(false)
  }
  // const renderButtons = () => {
  //   return renderButtonsComponent(`companyModuloData`, handleCancelClick)
  // }

  const onDragStart = (e: any) => {
    e.itemData = campos[e.fromIndex]
  }

  const onReorder = (e: any) => {
    const itemSelected = plantillaState?.plantillaCampos![e.fromIndex]
    let plantillaCampos =
      plantillaState.plantillaCampos != undefined
        ? plantillaState.plantillaCampos
        : []
    setCampos([{}])
    plantillaCampos = [
      ...plantillaCampos!.slice(0, e.fromIndex),
      ...plantillaCampos!.slice(e.fromIndex + 1),
    ]
    plantillaCampos = [
      ...plantillaCampos.slice(0, e.toIndex),
      itemSelected,
      ...plantillaCampos.slice(e.toIndex),
    ]
    dispatch(addPlantillaCampos(plantillaCampos))
  }
  return (
    <>
      {/* {isAllowed(PermisoType.AccesoExpedientes, userState!.user) ? ( */}
      {true ? (
        <>
          <div
            className={'content-block dx-card responsive-paddings'}
            style={{
              borderColor: '#f4f9ff',
              borderWidth: '2px',
              margin: '0.6%',
            }}
          >
            <ScrollView width="100%" height="100%" showScrollbar="always">
              <Sortable
                id="list"
                dropFeedbackMode={'push'}
                itemOrientation={'vertical'}
                dragDirection={'both'}
                scrollSpeed={30}
                scrollSensitivity={60}
                handle={''}
                onDragStart={(e: any) => {
                  onDragStart(e)
                }}
                onReorder={(e: any) => {
                  onReorder(e)
                }}
              >
                {plantillaState.plantillaCampos?.map((campo) => (
                  <div
                    key={campo.Id as number}
                    className="item dx-card dx-theme-text-color dx-theme-background-color"
                  >
                    <div className="settings">
                      <div className="column">
                        <div className="field">
                          <div className="value">
                            <h4>{campo.Nombre}</h4>
                          </div>
                        </div>
                      </div>
                      <div className="column">
                        <div className="field">
                          <div className="label">Ancho</div>
                          &nbsp;
                          <div
                            className="value"
                            title={campo.IdCampo.toString()}
                          >
                            <NumberBox
                              value={campo.Ancho as number}
                              onValueChanged={(e: any) => {
                                handleUpdatedPlantilla(
                                  parseInt(e.element.parentElement.title),
                                  {
                                    Ancho: e.value,
                                  },
                                )
                              }}
                              showSpinButtons={true}
                              max={100}
                              min={20}
                              format="#0px"
                            />
                          </div>
                        </div>
                      </div>
                      &nbsp;
                      <div className="column">
                        <div className="field">
                          <div className="label">Visible</div>
                          &nbsp;
                          <div
                            className="value"
                            title={campo.IdCampo.toString()}
                          >
                            <Switch
                              value={campo.Visible as boolean}
                              onValueChanged={(e: any) => {
                                handleUpdatedPlantilla(
                                  parseInt(e.element.parentElement.title),
                                  {
                                    Visible: e.value,
                                  },
                                )
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </Sortable>
            </ScrollView>
          </div>
        </>
      ) : (
        <div>
          <NotFound data={{ type: 1 }}></NotFound>
        </div>
      )}
    </>
  )
}

export default PlantillaCampos
