import { ADD_COMPONENTE, ADD_COMPONENTES, CLEAR_COMPONENTE, ComponenteActionsTypes, ComponenteState } from "./componenteType";



 const initialState: ComponenteState = {

 componente: undefined,
  componentes:undefined,

}; 

 export const componenteReducer = (
  state = initialState,
  action: ComponenteActionsTypes
): ComponenteState => {
  switch (action.type) {
    
    case ADD_COMPONENTE: {
      return { ...state, componente: action.payload };
    }
    case ADD_COMPONENTES: {
         return { ...state, componentes: action.payload };
     }
    case CLEAR_COMPONENTE: {
      return { ...state, componente: action.payload };
    }
    default:
      return state;
  }
}; 
