import { ModuloComponenteModel } from '../../models/moduloComponenteModel/moduloComponenteModel'
import { ModuloComponenteCampoDatoModel } from '../../models/moduloComponenteModel/moduloComponenteCampoDatoModel'
import {
  ADD_PLANTILLA,
  ADD_PLANTILLA_CAMPOS,
  PlantillaActionsTypes,
} from './plantillaComponenteType'
import { PlantillaModel } from '../../models/plantillaModel/plantillaModel'
import { PlantillaCamposModel } from '../../models/plantillaCamposModel/plantillaCamposModel'

export const addPlantilla = (
  plantilla: Partial<PlantillaModel> | undefined,
): PlantillaActionsTypes => ({
  type: ADD_PLANTILLA,
  payload: plantilla,
})

export const addPlantillaCampos = (
  plantillaCampos: PlantillaCamposModel[] | undefined,
): PlantillaActionsTypes => ({
  type: ADD_PLANTILLA_CAMPOS,
  payload: plantillaCampos,
})
