import { useState, ReactElement, useEffect, useRef } from 'react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Popup, ToolbarItem } from 'devextreme-react/popup'
import ScrollView from 'devextreme-react/scroll-view'
import { SharedGrid } from 'react-library-grid'
import { useHistory } from 'react-router-dom'
import { RootState } from '../../../../redux/combineReducers'
import {
  getUsuario,
  getStructure,
  getMapperUsuario,
  deleteUsuario,
} from '../services/usuarioServices'
import {
  addHideLoader,
  addShowLoader,
} from '../../../../redux/actions/configActions'
import NotFound from '../../../../components/notFound/notFound'
import { addRoles, addUsuario, clearUsuario } from '../redux/usuarioActions'
import { UsuarioCampoDatoModel } from '../models/usuarioCampoDatoModel'
import { UsuarioModel } from '../models/usuarioModel'
import UsuarioFormPage from './usuarioFormPage'
import { getSujetos } from '../../../companyAdministracionPages/sujetoPage/service/sujetoService'
import { addSujetos } from '../../../companyAdministracionPages/sujetoPage/redux/sujetoActions'
import { getRoles } from '../../../../services/roleService'
import {
  isAdministrador,
  isNormalUserByIdRole,
  isRolAllowed,
  isSuperAdministrador,
  modulosClaims,
} from '../../../../utils/allowAuthUtil'
import { modulos } from '../../../../utils/allowModuloUtil'
import { confirm, custom } from 'devextreme/ui/dialog'
import { ButtonColumnModel, DataGridModel } from 'shared-models'
import { addDataFormChange } from '../../../../redux/actions/sharedUtilsActions'
import { addPerfiles } from '../../../administracionPages/perfilPage/redux/perfilActions'
import { getPerfiles } from '../../../administracionPages/perfilPage/service/perfilService'
import {
  NotifyType,
  refreshGrid,
  showToast,
} from '../../../../utils/sharedUitls'
import { useScreenSize, useScreenSizeGrid } from '../../../../utils/media-query'


function UsuarioPage(): ReactElement {
  const dispatch = useDispatch()
  const userState = useSelector((state: RootState) => state.user)
  const sujetoState = useSelector((state: RootState) => state.sujeto)
  const [structure, setStructure] = useState(new UsuarioCampoDatoModel())
  const [rowFocused, setRowFocused] = useState('')
  const [showForm, setShowForm] = useState(false)
  const [showFormDatoUsuario, setShowFormDatoUsuario] = useState(false)
  const [rowSelected, setRow] = useState(0)
  const [loading, setLoading] = useState(false)
  const history = useHistory()
  const gridRef: any = useRef(null)
  const usuarioState = useSelector((state: RootState) => state.usuario?.usuario)
  const isChangedState = useSelector(
    (state: RootState) => state.sharedUtils.isChanged,
  )
  const perfilState = useSelector((state: RootState) => state.perfil)
  const { isLarge, isMedium, isSmall, isXSmall } = useScreenSize()
  const gridheight = useScreenSizeGrid()
  useEffect(() => {
    async function fetchData() {
      try {
        localStorage.removeItem('storage')
        setStructure(await getStructure(userState!.user!))
        await dispatch(addSujetos(await getSujetos(userState!.user!)))
        await dispatch(addRoles(await getRoles(userState!.user!)))
        await dispatch(addPerfiles(await getPerfiles(userState!.user!)))
        dispatch(addHideLoader(false))
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [])

  const handleOpenCreatePopup = async () => {
    if (
      isAdministrador(userState!.user!) ||
      isSuperAdministrador(userState!.user!)
    ) {
      dispatch(addSujetos(await getSujetos(userState!.user!)))
      dispatch(
        addUsuario({
          ...new UsuarioModel(),
          IdRole: 0,
          IdSujeto: sujetoState?.sujetos?.Datos[0].Id,
        }),
      )
      dispatch(addDataFormChange(false))
      setShowForm(!showForm)
    }
  }

  function fillDataGrid(): DataGridModel {
    const buttonColumn: ButtonColumnModel = new ButtonColumnModel({
      text: '',
      icon: `edit`,
      hint: 'Editar',
      cellRender: async (row: any) => {
        const usuario: UsuarioModel = getMapperUsuario(
          await getUsuario(userState!.user!, row.data.Id),
        )
        // if (
        //   isSuperAdministrador(userState!.user!) ||
        //   (isAdministrador(userState!.user!) &&
        //     userState!.user!.UserName == usuario.NombreUsuario)
        // ) {
        dispatch(addShowLoader(true))
        setRow(row.data)
        dispatch(addSujetos(await getSujetos(userState!.user!)))
        if (usuario.RolesTipoCuenta == null) {
          usuario.RolesTipoCuenta = []
        }
        dispatch(addUsuario(usuario))
        dispatch(addDataFormChange(false))
        setShowForm(!showForm)
        dispatch(addShowLoader(false))
        // } else {
        //   showToast(
        //     'Actualmente no se puede editar o eliminar un usuario Administrador',
        //     NotifyType.info,
        //     5000,
        //   )
        // }
      },
    })
    const buttonColumn2: ButtonColumnModel = new ButtonColumnModel({
      text: '',
      icon: `trash`,
      hint: 'Elimimar',
      cellRender: async (row: any) => {
        let confirmDialog = confirm(
          '<i>¿Desear eliminar este usuario?</i>',
          'Confirma los cambios',
        )
        confirmDialog.then(async (dialogResult) => {
          if (dialogResult) {
            const usuario: UsuarioModel = getMapperUsuario(
              await getUsuario(userState!.user!, row.data.Id),
            )
            if (
              // isSuperAdministrador(userState!.user!) &&
              userState!.user?.UserName != usuario.NombreUsuario
            ) {
              dispatch(addShowLoader(true))
              await deleteUsuario(userState!.user!, row.data.Id)
              setTimeout(() => {
                refreshGrid(
                  {
                    setShowForm: setShowForm,
                  },
                  showForm,
                )
                dispatch(addShowLoader(false))
              }, 2000)
            } else {
              showToast(
                'No se pueden eliminar usuarios administradores,contacte con la empresa gestora de software',
                NotifyType.info,
                5000,
              )
            }
          } else {
          }
        })
      },
    })
    let dataGridModel = new DataGridModel({
      dataGridName: 'usuarioGridComponent',
      height:gridheight ,
      ref: gridRef,
      rowFocused: rowFocused,
      columns: structure.Campos,
      openRow: ({}) => {},
      openRowEnterKey: ({}) => {},
      focusEventChanged: (e: any) => {
        if (e.row) {
          setRowFocused(e.row.key)
        }
      },
      focusEventChanging: ({}) => {},
      openCreatePopup: handleOpenCreatePopup,
      user: userState!.user!,
      url: `${process.env.REACT_APP_API_URL_CORE}/Usuario/Grid/FilterGroup`,
      urlAux: `${process.env.REACT_APP_API_URL_CORE}/Usuario/Grid`,
      buttonsColumns: [buttonColumn, buttonColumn2],
      filterRow: true,
      filterPanel: true,
      columnChooser: true,
      headerFilter: false,
      exportData: false,
      showSearchPanel: true,
      showToolbar: true,
      history: history,
      singularName: 'Usuario',
      pluralName: 'Usuarios',
      pageName: 'Usarios', //TODO: revisar esta propiedad sera la pagina que nos quiera abrir en history de create
      openedPopup: showForm,
    })

    return dataGridModel
  }
  const preventClose = (data: any) => {
    setLoading(true)

    // let confirmDialog = confirm(
    //   '<i>¿Salir sin guardar?</i>',
    //   'Confirma los cambios',
    // )
    // confirmDialog.then((dialogResult) => {
    //   if (dialogResult) {
    //     setShowForm(false)
    //   } else {
    //     setShowForm(true)\
    //     setLoading(false)
    //   }
    // })

    setShowForm(true)
    setLoading(false)
  }
  return (
    <>
      {isRolAllowed(userState!.user!, modulos.ADMINISTRACION, [
        modulosClaims.VIEW,
      ]) ? (
        <div
          className={'content-block dx-card'}
          style={{
            borderColor: '#f4f9ff',
            borderWidth: '2px',
            margin: '0.3%',
            padding: '0.16%',
          }}
        >
          <h3 style={{ margin: 0, padding: 0 }}>Usuarios</h3>
          {structure?.Campos != undefined && structure?.Campos.length > 0 ? (
            <div>
           
                  <SharedGrid
                    key="dataGridUsuarios"
                    dataGridModel={fillDataGrid()}
                  />
             
            </div>
          ) : null}
          <Popup
            visible={showForm}
             onHiding={(e) => {
              if (isChangedState) {
                preventClose(e)
              } else {
                dispatch(addDataFormChange(false))
              }
              dispatch(clearUsuario(new UsuarioModel()))
              setShowForm(!showForm)
            }} 
     
           
            dragEnabled={true}
            closeOnOutsideClick={false}
            showTitle={true}
            title={`Usuario ${usuarioState?.Id?usuarioState?.Id:""}`}
            width={'90%'}
            height={'90%'}
            showCloseButton={false}
          >
            <ToolbarItem
            widget="dxButton"
            toolbar="top"
            location="after"
            options={{ text: '',
            icon:"close",
            onClick: ()=>{
              if(isChangedState){
                
                  const buttonAttributes={
                    id: 'usuarioCancelButton',
                    class: 'usuarioCancelButtonClass'
                  }
                  let confirmDialog = custom({
                    title: '',
                    
                    messageHtml:
                      '<b>Para conservar los cambios antes pulsar Guardar, Si continua perderá todos los datos cambiados</b>',
                    buttons: [
                      {
                        text: 'Continuar',
                        onClick: (e) => {
                          setShowForm(false)
                          
                          dispatch(clearUsuario(new UsuarioModel()))
                        
                  
                          confirmDialog.hide()
                        },
                      },
                      {
                        
                        text: 'Cancelar',
                        
                        onClick: (e) => {
                          confirmDialog.hide()
                        },
                      },
                    ],
                  })
                  confirmDialog.show().then((dialogResult: any) => {
                    console.log(dialogResult)
                  })
                } else {
                  setShowForm(false)
                 
                  dispatch(clearUsuario(new UsuarioModel()))
                
                 
                }
              }
              }
            }
          />
            <ScrollView width="100%" height="100%">
              <UsuarioFormPage
                row={rowSelected}
                setShowForm={setShowForm}
                showForm={showForm}
              ></UsuarioFormPage>
            </ScrollView>
          </Popup>
        </div>
      ) : (
        <div
          className={'content-block dx-card responsive-paddings'}
          style={{ borderColor: '#f4f9ff', borderWidth: '2px', margin: '0.6%' }}
        >
          <NotFound data={{ type: 1 }}></NotFound>
        </div>
      )}
      {}
    </>
  )
}

export default UsuarioPage

