import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'

import {
  logType,
  methodsEndPointType,
  customLog,
  checkStatusCode,
} from 'shared-utils'
import { ForgotPasswordModel, UserModel } from 'shared-models'

import { NotifyType, showToast } from '../../../../utils/sharedUitls'

import { SujetosCampoDatoModel } from '../models/sujetosCampoDatoModel'
import { SujetoModel } from '../models/sujetoModel'
import { authHeader } from 'shared-services'

const serviceName = 'authService'

export const getSujetos = async (
  user: UserModel,
): Promise<SujetosCampoDatoModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/sujeto`
    let SujetosData: SujetosCampoDatoModel = new SujetosCampoDatoModel()
    let datos
    await axios({
      url: url,
      method: 'GET',
      headers: authHeader(user!),
    })
      .then((response) => {
        SujetosData = jsonConvert.deserializeObject(
          response.data,
          SujetosCampoDatoModel,
        )
      })
      .catch((error) => {
        showToast(
          'No se han podido obtener los sujetos.',
          NotifyType.error,
          5000,
        )
      })

    return SujetosData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
export async function getSujeto(
  user: UserModel,
  id: number,
): Promise<SujetoModel> {
  let jsonConvert: JsonConvert = new JsonConvert()
  jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
  let url = `${process.env.REACT_APP_API_URL_CORE}/Sujeto/${id}`

  let sujeto: SujetoModel = new SujetoModel()
  await axios({
    url: url,
    method: methodsEndPointType.GET,
    headers: authHeader(user!),
  })
    .then((response) => {
      checkStatusCode(response.status)
      sujeto = jsonConvert.deserializeObject(
        response.data.Datos[0],
        SujetoModel,
      )
    })
    .catch((error) => {
      customLog(
        null,
        logType.ERROR,
        serviceName,
        getSujeto.name,
        methodsEndPointType.GET,
      )
      return {}
    })

  return sujeto
}
export async function postSujeto(
  user: UserModel,
  sujeto: SujetoModel,
): Promise<boolean> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/Sujeto`

    const headers = {
      Authorization: 'Bearer my-token',
      'My-Custom-Header': 'foobar',
    }

    await axios.post(url, sujeto, { headers }).then((response) => {
      checkStatusCode(response.status)
      customLog(
        `sujeto creado ${response.data.Id}`,
        logType.INFO,
        serviceName,
        putSujeto.name,
        methodsEndPointType.POST,
      )
      showToast('Compañia creada', NotifyType.success, 5000)
    })
    return true
  } catch (error) {
    customLog(
      `Error al crear Sujeto => ${error.message}`,
      logType.ERROR,
      serviceName,
      postSujeto.name,
      methodsEndPointType.POST,
    )
    return false
  }
}
export async function putSujeto(
  user: UserModel,
  sujeto: SujetoModel,
): Promise<boolean> {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL
    let url = `${process.env.REACT_APP_API_URL_CORE}/Sujeto`

    const headers = {
      Authorization: 'Bearer my-token',
      'My-Custom-Header': 'foobar',
    }
    await axios
      .put(url, sujeto, { headers })
      .then((response) => {
        checkStatusCode(response.status)
        customLog(
          `sujeto modificado ${sujeto.Id}`,
          logType.INFO,
          serviceName,
          putSujeto.name,
          methodsEndPointType.PUT,
        )
        showToast('Compañia editada', NotifyType.success, 5000)
      })
      .catch((error) => {
        customLog(
          `Error al editar Sujeto => ${error.message}`,
          logType.ERROR,
          serviceName,
          putSujeto.name,
          methodsEndPointType.PUT,
        )
      })
    return true
  } catch (error) {
    return false
  }
}
