import { useState, ReactElement, useEffect, useRef, useMemo } from 'react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../../../redux/combineReducers'
import NotFound from '../../../../components/notFound/notFound'
import { List } from 'devextreme-react'
import ArrayStore from 'devextreme/data/array_store'
import { postCompany, putCompany } from '../services/companiesServices'
import { CompanyModuloModel } from '../models/companyModuloModel'
import { addCompany, addCompanyModulos } from '../redux/companiesActions'
import '../pages/scss/companyModulo.scss'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'
import Form, { SimpleItem } from 'devextreme-react/form'
import { useScreenSize } from '../../../../utils/media-query'
import { useForm } from 'react-hook-form'
import { isSuperAdministrador } from '../../../../utils/allowAuthUtil'
import {
  NotifyType,
  refreshGrid,
  showToast,
} from '../../../../utils/sharedUitls'
import { addParentEnlaces } from '../redux/Enlace/enlaceActions'
import { getEnlaces } from '../services/enlacesService'

function ModulosList(props: any): ReactElement {
  const dispatch = useDispatch()
  const moduloState = useSelector((state: RootState) => state.modulo)
  const companyState = useSelector((state: RootState) => state.company)
  const userState = useSelector((state: RootState) => state.user)
  const company = useSelector((state: RootState) => state.company?.company)
  const calculadoraState = useSelector(
    (state: RootState) =>
      state.calculadoraTransferencias?.calculadoraTransferencias,
  )
  const emailState = useSelector((state: RootState) => state.email?.email)
  const enlaceState = useSelector((state: RootState) => state.enlace)
  const isChangedState = useSelector(
    (state: RootState) => state.sharedUtils.isChanged,
  )
  const [selectedItemKeys, setSelecteItemKeys] = useState<number[] | undefined>(
    [],
  )
  const [dataModules, setDataModules] = useState([{}])
  const [loading, setLoading] = useState(false)
  const { isLarge } = useScreenSize()
  const formData = useRef({})
  const { register, handleSubmit } = useForm()
  var ismounted = false
  useMemo(() => {
    function fetchData() {
      setSelecteItemKeys([])
      try {
        if (companyState?.companyModulos) {
          setSelecteItemKeys(
            companyState?.companyModulos?.map((x: any) => x.IdModulo),
          )
        } else {
          setSelecteItemKeys([])
        }

        if (moduloState.modulos) {
          let data: any = []
          moduloState.modulos.Datos.map((x) => {
            return data.push({ id: x.Id, text: x.Nombre })
          })
          setDataModules(data)
        }
        ismounted = true
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [props.showForm])
  const onSubmit = async (data: any) => {
    setLoading(true)
    let result: boolean = false
    if (!company?.Id) {
      dispatch(addCompany({ ...company, Id: 0 }))
    }
    if (company?.Id) {
      result = await putCompany(
        userState!.user!,
        company,
        calculadoraState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    } else {
      result = await postCompany(
        userState!.user!,
        company!,
        calculadoraState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    }

    if (result) {
      setLoading(false)
      refreshGrid(props, true)
      props.setShowForm(false)
      localStorage.setItem(
        'parentCompanyModulos',
        JSON.stringify(companyState?.companyModulos),
      )
      localStorage.setItem(
        'parentEnlaces',
        JSON.stringify(
          dispatch(
            addParentEnlaces(
              await getEnlaces(
                userState?.user!,
                parseInt(userState?.user!.CompanyId!),
              ),
            ),
          ).payload,
        ),
      )
    } else {
      setLoading(false)
    }
  }
  const saveCompanyModulos = (property: any) => {
    let companyModulos: CompanyModuloModel[] = []
    property?.map((key: any) => {
      return companyModulos.push({
        IdCompany: companyState?.company?.Id!,
        IdModulo: key,
        Nombre: moduloState?.modulos?.Datos.find((x) => x.Id == key)?.Nombre!,
      })
    })
    dispatch(addCompanyModulos(companyModulos))
  }
  const dataSource: any = new ArrayStore({
    key: 'id',
    data: dataModules,
  })
  const handleCancelClick = () => {
    dispatch(addCompanyModulos(undefined))
    props.setShowForm(false)
    /*  calculadoraFormRef.current.instance.resetValues() */
  }
  const renderButtons = () => {
    return renderButtonsComponent(`companyModuloData`, handleCancelClick)
  }

  const handleOptionChanged = (event: any) => {
    if (event.name == 'selectedItemKeys') {
      if (JSON.stringify(event.value) != JSON.stringify(event.previousValue)) {
        if (isSuperAdministrador(userState!.user!)) {
          setSelecteItemKeys(event.previousValue)
          saveCompanyModulos(event.previousValue)
        } else {
          showToast(
            'No se pueden actualizar los módulos contratados, por favor ponte en contacto con tu proovedor',
            NotifyType.error,
            3000,
          )
        }
      }
    }
  }

  const companyModuloslistRef: any = useRef(null)

  if (!props.showForm) {
    if (companyModuloslistRef.current.current != null) {
      // companyModulosFormRef.current.instance.repaint()
      //companyModuloslistRef.current.instance.repaintChangesOnly(true)
      setSelecteItemKeys([])
    }
  }

  return (
    <>
      {/* {isAllowed(PermisoType.AccesoExpedientes, userState!.user) ? ( */}
      {true ? (
        <>
          <div
            className={'content-block dx-card responsive-paddings'}
            style={{
              borderColor: '#f4f9ff',
              borderWidth: '2px',
              margin: '0.6%',
            }}
          >
            <List
              dataSource={dataSource}
              ref={companyModuloslistRef}
              height={'100%'}
              showSelectionControls={true}
              selectionMode={'all'}
              selectAllMode={'allPages'}
              disabled={isSuperAdministrador(userState!.user!) ? false : true}
              selectedItemKeys={selectedItemKeys}
              elementAttr={{ id: 'companyModulosList' }}
              onOptionChanged={handleOptionChanged}
            ></List>
          </div>
          {/*      <form
            className={'legalForm'}
            onSubmit={handleSubmit(onSubmit)}
            style={{ width: '100%' }}
          >
            <Form
              formData={formData.current}
              disabled={loading}
              ref={companyModulosFormRef}
              labelLocation={isLarge ? 'left' : 'top'}
              validationGroup="companyModulosData"
            >
              {isSuperAdministrador(userState!.user!) ? (
                <SimpleItem render={renderButtons} cssClass="alignBottom" />
              ) : null} 
            </Form>
          </form> */}
        </>
      ) : (
        <div>
          <NotFound data={{ type: 1 }}></NotFound>
        </div>
      )}
    </>
  )
}

export default ModulosList
