import {
  ADD_LEGAL_ADMINISTRACION,
  AdministracionLegalState,
  AdministracionEmailState,
  AdministracionActionsTypes,
  ADD_EMAIL_ADMINISTRACION,
} from "./administracionEmailType";



const initialState: AdministracionEmailState = {
  email: undefined,
};

const administracionEmailReducer = (
  state = initialState,
  action: AdministracionActionsTypes
): AdministracionEmailState | undefined => {
  switch (action.type) {
    case ADD_EMAIL_ADMINISTRACION: {
      return { ...state, email: action.payload };

    }
    default:
      return state;
  }
};
export { administracionEmailReducer };
