import React, { ReactElement } from "react";
import { useSelector } from "react-redux";
import { RootState } from "../../redux/combineReducers";
import "./accesoSoloCalculadoraPage.scss";
import { useClearCache } from "react-clear-cache";
import calculadoraImg from "../../assets/Calculadora.png";

function AccesoSoloCalculadoraPage(): ReactElement {
  const { isLatestVersion, emptyCacheStorage } = useClearCache();

  const usuarioState = useSelector((state: RootState) => state.user);

  return (
    <>
      {/*
       **Modificación TODO Probar, Esto ya deberia de funcionar
       *TODO cuando dejo esto activo en el menu o el login afectan a que no funcione el debugger de VS */}
      {!isLatestVersion && emptyCacheStorage()}

      <div className="center">
        <a
          href={
            "https://portal.elgestor.com/CalculadoraIntro.aspx?Id=" +
            usuarioState!.user?.CodigoCalculadora
          }
          target="_blank"
          rel="noopener noreferrer"
        >
          <img
            className="calculadoraImg"
            src={calculadoraImg}
            alt="LogoCalculadora"
          />
        </a>
      </div>
    </>
  );
}

export default AccesoSoloCalculadoraPage;
