import { EnlaceModel } from '../../models/enlaceModel'
import { EnlacesModel } from '../../models/enlacesModel'

export interface EnlaceState {
  enlace: EnlaceModel | undefined
  enlaces: EnlaceModel[] | []
  parentEnlaces: EnlaceModel[] | undefined
}

export const ADD_ENLACE = 'ADD_ENLACE'
export const ADD_ENLACES = 'ADD_ENLACES'
export const ADD_PARENT_ENLACES = 'ADD_PARENT_ENLACES'
export const CLEAR_ENLACE = '@todo/CLEAR_ENLACE'

interface AddEnlaceRequest {
  type: typeof ADD_ENLACE
  payload: Partial<EnlaceModel> | undefined
}
interface AddEnlacesRequest {
  type: typeof ADD_ENLACES
  payload: Partial<EnlacesModel[]> | undefined
}
interface AddParentEnlacesRequest {
  type: typeof ADD_PARENT_ENLACES
  payload: Partial<EnlacesModel[]> | undefined
}
interface ClearEnlaceRequest {
  type: typeof CLEAR_ENLACE
  payload: EnlaceModel | undefined
}

export type EnlaceActionsTypes =
  | AddEnlaceRequest
  | ClearEnlaceRequest
  | AddEnlacesRequest
  | AddParentEnlacesRequest
