import { PerfilModel } from '../models/perfilModel'
import { PerfilesCampoDatoModel } from '../models/perfilCampoDatoModel'
import {
  SujetoActionsTypes,
  ADD_PERFILES,
  ADD_PERFIL,
  CLEAR_PERFIL,
} from './perfilType'

export const addPerfil = (
  perfil: Partial<PerfilModel> | PerfilModel | undefined,
): SujetoActionsTypes => ({
  type: ADD_PERFIL,
  payload: perfil,
})

export const clearPerfil = (
  clearPerfil: PerfilModel | undefined,
): SujetoActionsTypes => ({
  type: CLEAR_PERFIL,
  payload: clearPerfil,
})
export const addPerfiles = (
  perfiles: PerfilModel[] | undefined,
): SujetoActionsTypes => ({
  type: ADD_PERFILES,
  payload: perfiles,
})
