import {
  ADD_PLANTILLA,
  ADD_PLANTILLA_CAMPOS,
  PlantillaActionsTypes,
  PlantillaState,
} from './plantillaComponenteType'

const initialState: PlantillaState = {
  plantilla: undefined,
  plantillaCampos: undefined,
}

export const plantillaReducer = (
  state = initialState,
  action: PlantillaActionsTypes,
): PlantillaState => {
  switch (action.type) {
    case ADD_PLANTILLA: {
      return { ...state, plantilla: action.payload }
    }
    case ADD_PLANTILLA_CAMPOS: {
      return { ...state, plantillaCampos: action.payload }
    }
    default:
      return state
  }
}
