import { ModuloModel } from '../../models/moduloModel/moduloModel'
import { ModuloCampoDatoModel } from '../../models/moduloModel/moduloCampoDatoModel'
import {
  ADD_MODULO,
  ADD_MODULOS,
  CLEAR_MODULO,
  ModuloActionsTypes,
} from './moduloType'

export const addModulo = (
  sujeto: Partial<ModuloModel> | undefined,
): ModuloActionsTypes => ({
  type: ADD_MODULO,
  payload: sujeto,
})

export const clearModulo = (
  sujeto: ModuloModel | undefined,
): ModuloActionsTypes => ({
  type: CLEAR_MODULO,
  payload: sujeto,
})
export const addModulos = (
  sujetos: ModuloCampoDatoModel | undefined,
): ModuloActionsTypes => ({
  type: ADD_MODULOS,
  payload: sujetos,
})
