import {
  ADD_LEGAL_ADMINISTRACION,
  AdministracionLegalState,
  AdministracionEmailState,
  AdministracionActionsTypes,
  ADD_EMAIL_ADMINISTRACION,
} from "../../emailAdministracion/redux/administracionEmailType";



const initialState: AdministracionLegalState = {

  legal: undefined,
};

const administracionLegalReducer = (
  state = initialState,
  action: AdministracionActionsTypes
): AdministracionLegalState | undefined => {
  switch (action.type) {
    case ADD_LEGAL_ADMINISTRACION: {
      return { ...state, legal: action.payload };
    }
    default:
      return state;
  }
};

export { administracionLegalReducer };
