
import { CalculadoraTransferenciaModel } from '../../models/calculadoraTransferenciaModel'
import {

  ADD_CALCULADORA_TRANSFERENCIAS,
  CLEAR_CALCULADORA_TRANSFERENCIAS,
  CalculadoraTransferenciasActionsTypes,
} from './calculadoraTransferenciasType'


export const addCalculadoraTranseferencias = (
  calculadoraTransferencias: Partial<CalculadoraTransferenciaModel> | CalculadoraTransferenciaModel | undefined,
): CalculadoraTransferenciasActionsTypes => ({
  type: ADD_CALCULADORA_TRANSFERENCIAS,
  payload: calculadoraTransferencias,
})


export const clearCalculadoraTranseferencias = (
  calculadoraTransferencias: CalculadoraTransferenciaModel | undefined,
): CalculadoraTransferenciasActionsTypes => ({
  type: CLEAR_CALCULADORA_TRANSFERENCIAS,
  payload: calculadoraTransferencias,
})
