import { ModuloModel } from '../../models/moduloModel/moduloModel'
import { ModuloCampoDatoModel } from '../../models/moduloModel/moduloCampoDatoModel'

export interface ModuloState {
  modulo: ModuloModel | undefined
  modulos: ModuloCampoDatoModel | undefined
}

export const ADD_MODULOS = '@todo/ADD_MODULOS'
export const ADD_MODULO = '@todo/ADD_MODULO'
export const CLEAR_MODULO = '@todo/CLEAR_MODULO'

interface AddModuloRequest {
  type: typeof ADD_MODULO
  payload: Partial<ModuloModel> | undefined
}
interface AddModulosRequest {
  type: typeof ADD_MODULOS
  payload: ModuloCampoDatoModel | undefined
}
interface ClearModuloRequest {
  type: typeof CLEAR_MODULO
  payload: ModuloModel | undefined
}

export type ModuloActionsTypes =
  | AddModuloRequest
  | AddModulosRequest
  | ClearModuloRequest
