import {
  ADD_USUARIOS,
  ADD_USUARIO,
  CLEAR_USUARIO,
  ADD_ROLES,
  UsuarioActionsTypes,
  UsuarioState,
} from './usuarioType'

const initialState: UsuarioState = {
  usuario: undefined,
  usuarios: undefined,
  roles: undefined,
}

export const usuarioReducer = (
  state = initialState,
  action: UsuarioActionsTypes,
): UsuarioState | undefined => {
  switch (action.type) {
    case ADD_USUARIOS: {
      return { ...state, usuarios: action.payload }
    }
    case ADD_USUARIO: {
      return { ...state, usuario: action.payload }
    }
    case CLEAR_USUARIO: {
      return { ...state, usuario: action.payload }
    }
    case ADD_ROLES: {
      return { ...state, roles: action.payload }
    }
    default:
      return state
  }
}
