import { useState, ReactElement, useEffect, useRef } from 'react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Popup, ToolbarItem } from 'devextreme-react/popup'
import ScrollView from 'devextreme-react/scroll-view'
import { SharedGrid } from 'react-library-grid'

import { useHistory } from 'react-router-dom'
import { RootState } from '../../../../redux/combineReducers'
import {
  deleteCompany,
  getCompany,
  getCompanyModulos,
  getStructure,
} from '../services/companiesServices'
import {
  addHideLoader,
  addShowLoader,
} from '../../../../redux/actions/configActions'
import NotFound from '../../../../components/notFound/notFound'
import {
  addCompany,
  addCompanyModulos,
  addParentCompany,
} from '../redux/companiesActions'
import { CompanyModel } from '../models/companyModel'
import { confirm, custom } from 'devextreme/ui/dialog'
import { CompanyCampoDatoGridModel } from '../models/companyCampoDatoModel'
import CompanyFormTabsPage from './CompanyFormTabsPage'
import { addModulos } from '../../plantillasAdministracion/redux/modulo/moduloActions'
import { getModulos } from '../../plantillasAdministracion/services/moduloServices'
import { addCalculadoraTranseferencias } from '../redux/CalculadoraTransferencias/calculadoraTransferenciasActions'
import { getCalculadora } from '../services/calculadoraService'
import { addEnlaces } from '../redux/Enlace/enlaceActions'
import { getEnlaces } from '../services/enlacesService'
import { addEmail } from '../redux/Email/emailActions'
import { getEmail } from '../services/emailService'
import { CalculadoraTransferenciaModel } from '../models/calculadoraTransferenciaModel'
import { ConfiguracionEnvioEmailModel } from '../models/configuracionEnvioEmailModel'
import {
  isRolAllowed,
  isSuperAdministrador,
  modulosClaims,
} from '../../../../utils/allowAuthUtil'
import { modulos } from '../../../../utils/allowModuloUtil'
import { addDataFormChange } from '../../../../redux/actions/sharedUtilsActions'

import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { ButtonColumnModel, DataGridModel } from 'shared-models'
import { useScreenSize, useScreenSizeGrid } from '../../../../utils/media-query'
// "react-library-grid": "^3.1.5",
const position = { of: 'popupCompany' }
function CompaniesIndex(): ReactElement {
  const dispatch = useDispatch()
  const userState = useSelector((state: RootState) => state.user)
  const [structure, setStructure] = useState(new CompanyCampoDatoGridModel())
  const isChangedState = useSelector(
    (state: RootState) => state.sharedUtils.isChanged,
  )
  const [rowFocused, setRowFocused] = useState('')
  const [showForm, setShowForm] = useState(false)
  const [rowSelected, setRow] = useState(0)
  const history = useHistory()
  const popupCompaynyRef = useRef(null)
  const company = useSelector(
    (state: RootState) => state.company?.parentCompany,
  )


  const { isXSmall, isSmall, isMedium, isLarge } = useScreenSize()
  const gridheight = useScreenSizeGrid()
  useEffect(() => {
    async function fetchData() {
      try {
        localStorage.removeItem('storage')
        await dispatch(addModulos(await getModulos(userState!.user!)))
        setStructure(await getStructure(userState!.user!))
        await dispatch(addHideLoader(false))

        dispatch(
          addParentCompany(
            await getCompany(
              userState!.user!,
              parseInt(userState!.user?.CompanyId!),
            ),
          ),
        )
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [])

  const openCreatePopup = async () => {
    if (isSuperAdministrador(userState!.user!)) {
      dispatch(addCompany(new CompanyModel()))
      dispatch(addEnlaces([]))
      dispatch(
        addCalculadoraTranseferencias(new CalculadoraTransferenciaModel()),
      )
      dispatch(addEmail(new ConfiguracionEnvioEmailModel()))
      dispatch(addDataFormChange(false))
      setShowForm(!showForm)
    } else {
      showToast(
        'Solo los administradores propietarios de la aplicación pueden crear nuevas gestorías',
        NotifyType.info,
        5000,
      )
    }
  }

  function fillDataGrid(): DataGridModel {
    const buttonColumnCreate: ButtonColumnModel = new ButtonColumnModel({
      text: '',
      icon: `edit`,
      hint: 'Editar',
      cellRender: async (row: any) => {
        dispatch(addShowLoader(true))
        setRow(row.data)
        dispatch(addCompany(await getCompany(userState!.user!, row.data.Id)))
        dispatch(
          addCalculadoraTranseferencias(
            await getCalculadora(userState?.user!, row.data.Id),
          ),
        )
        dispatch(addEnlaces(await getEnlaces(userState!.user!, row.data.Id)))
        dispatch(addEmail(await getEmail(userState!.user!, row.data.Id)))
        dispatch(
          addCompanyModulos(
            await getCompanyModulos(userState!.user!, row.data.Id),
          ),
        )
        dispatch(addDataFormChange(false))
        setShowForm(!showForm)
        dispatch(addShowLoader(false))
      },
    })
    const buttonColumnDelete: ButtonColumnModel = new ButtonColumnModel({
      text: '',
      icon: `trash`,
      hint: 'Eliminar',
      cellRender: async (row: any) => {
        setRow(row.data)
        let confirmDialog = confirm(
          '<i>¿Está seguro de que quiere eliminar esta gestaría por completo?</i>',
          'Confirma los cambios',
        )
        confirmDialog.then(async (dialogResult) => {
          if (dialogResult) {
            await deleteCompany(userState!.user!, row.data.Id)
            const refreshButton = document.getElementsByClassName(
              'dx-icon dx-icon-refresh',
            )[0] as HTMLElement
            refreshButton.click()
          } else {
          }
        })
      },
    })
    let dataGridModel = new DataGridModel({
      dataGridName: 'companyGridComponent',
      height: gridheight,
      rowFocused: rowFocused,
      columns: structure.Campos,
      ref: popupCompaynyRef,
      openRow: ({}) => {},
      openRowEnterKey: ({}) => {},
      focusEventChanged: (e: any) => {
        if (e.row) {
          setRowFocused(e.row.key)
        }
      },
      focusEventChanging: ({}) => {},
      openCreatePopup: openCreatePopup,
      user: userState!.user!,
      url: `${process.env.REACT_APP_API_URL_CORE}/Companies/Grid/FilterGroup`,
      urlAux: `${process.env.REACT_APP_API_URL_CORE}/Companies/Grid`,
      buttonsColumns: [buttonColumnCreate, buttonColumnDelete],
      filterRow: true,
      filterPanel: true,
      columnChooser: true,
      headerFilter: false,
      exportData: true,
      showSearchPanel: true,
      showToolbar: true,
      history: history,
      singularName: 'Compañia',
      pluralName: 'Compañias',
      pageName: 'Compañia', //TODO: revisar esta propiedad sera la pagina que nos quiera abrir en history de create
      openedPopup: showForm,
    })

    return dataGridModel
  }

  const preventClose = (data: any) => {
    let confirmDialog = confirm(
      '<i>¿Salir sin guardar?</i>',
      'Confirma los cambios',
    )
    confirmDialog.then((dialogResult) => {
      if (dialogResult) {
        setShowForm(false)
      } else {
        setShowForm(true)
      }
    })
  }

  return (
    <>
      {isRolAllowed(userState!.user!, modulos.ADMINISTRACION, [
        modulosClaims.VIEW,
      ]) ? (
        <div
          className={'content-block dx-card'}
          style={{
            borderColor: '#f4f9ff',
            borderWidth: '2px',
            margin: '0.3%',
            padding: '0.16%',
          }}
        >
          <h3 style={{ margin: 0, padding: 0 }}>Gestorías</h3>
          {structure?.Campos != undefined && structure?.Campos.length > 0 ? (
           
              
                <SharedGrid dataGridModel={fillDataGrid()}></SharedGrid>
            
          
          ) : null}
          <Popup
            visible={showForm}
            onHiding={(e) => {
              if (isChangedState) {
                preventClose(e)
              } else {
                dispatch(addDataFormChange(false))
              }
              setShowForm(!showForm)
            }}
            dragEnabled={true}
            closeOnOutsideClick={false}
            showTitle={true}
            title="Gestorías"
            width={'95%'}
            height={'95%'}
            showCloseButton={false}
          >
              <ToolbarItem
            widget="dxButton"
            toolbar="top"
            location="after"
            options={{ text: '',
            icon:"close",
            onClick: ()=>{
              if(isChangedState){
                
                  const buttonAttributes={
                    id: 'usuarioCancelButton',
                    class: 'usuarioCancelButtonClass'
                  }
                  let confirmDialog = custom({
                    title: '',
                    
                    messageHtml:
                      '<b>Para conservar los cambios antes pulsar Guardar, Si continua perderá todos los datos cambiados</b>',
                    buttons: [
                      {
                        text: 'Continuar',
                        onClick: (e) => {
                          setShowForm(false)
                          
                          dispatch(addCompany(new CompanyModel()))
                        
                  
                          confirmDialog.hide()
                        },
                      },
                      {
                        
                        text: 'Cancelar',
                        
                        onClick: (e) => {
                          confirmDialog.hide()
                        },
                      },
                    ],
                  })
                  confirmDialog.show().then((dialogResult: any) => {
                    console.log(dialogResult)
                  })
                } else {
                  setShowForm(false)
                 
                  dispatch(addCompany(new CompanyModel()))
                
                 
                }
              }
              }
            }
          />
            <ScrollView width="100%" height="100%">
              <CompanyFormTabsPage
                row={rowSelected}
                
                setShowForm={setShowForm}
                showForm={showForm}
                id="popupCompany"
              ></CompanyFormTabsPage>
            </ScrollView>
          </Popup>
        </div>
      ) : (
        <div
          className={'content-block dx-card responsive-paddings'}
          style={{ borderColor: '#f4f9ff', borderWidth: '2px', margin: '0.6%' }}
        >
          <NotFound data={{ type: 1 }}></NotFound>
        </div>
      )}
    </>
  )
}

export default CompaniesIndex
