import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Form } from 'devextreme-react'
import { GroupItem, RequiredRule, SimpleItem } from 'devextreme-react/form'
import { confirm } from 'devextreme/ui/dialog'
import { useForm } from 'react-hook-form'
import { RootState } from '../../../../redux/combineReducers'
import { useScreenSize } from '../../../../utils/media-query'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'
import PlantillaCampos from '../components/plantillaCamposComponent'
import { addPlantilla } from '../redux/plantilla/plantillaComponenteActions'
import { addPerfil } from '../../../administracionPages/perfilPage/redux/perfilActions'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import {
  getPlantillaWithChild,
  postPlantilla,
  putPlantilla,
} from '../services/plantillaService'

function PlantillaFormPage(props: any): ReactElement {
  const userState = useSelector((state: RootState) => state.user)
  const perfilState = useSelector((state: RootState) => state.perfil)
  const plantillaState = useSelector((state: RootState) => state.plantilla)
  const { isXSmall, isLarge } = useScreenSize()
  const [loading, setLoading] = useState(false)
  const formData = useRef({})
  const { register, handleSubmit } = useForm()
  const dispatch = useDispatch()
  useEffect(() => {
    async function fetchData() {}
    fetchData()
  }, [])

  const onSubmit = async (data: any) => {
    setLoading(true)

    let confirmDialog = confirm(
      '<i>¿Está seguro de que quiere guardar la información?</i>',
      'Confirma los cambios',
    )
    confirmDialog.then(async (dialogResult) => {
      if (dialogResult) {
        if (
          plantillaState?.plantilla?.Nombre != undefined &&
          plantillaState?.plantilla?.Nombre != '' &&
          perfilState.perfil != undefined
        ) {
          const extraData = {
            IdPlantillaParent: plantillaState?.plantillaCampos![0].IdPlantilla,
            IdPerfil: perfilState.perfil?.Id,
            Nombre: plantillaState?.plantilla?.Nombre,
          }
          let isCreatePlantilla: boolean = false
          if (plantillaState?.plantilla?.Id == 0) {
            isCreatePlantilla = await postPlantilla(
              userState!.user!,
              plantillaState?.plantillaCampos!,
              extraData,
            )
          } else {
            isCreatePlantilla = await putPlantilla(
              userState!.user!,
              plantillaState?.plantillaCampos!,
              extraData,
            )
          }

          if (isCreatePlantilla) {
            const plantillasChild = await getPlantillaWithChild(
              userState!.user!,
              plantillaState?.plantillaCampos![0].IdPlantilla as number,
            )
            props.setPlantillas(plantillasChild)
            setLoading(false)
            props.setShowForm(!props.showForm)
          } else {
            setLoading(false)
          }
        } else {
          setLoading(false)
          showToast(
            'La plantilla no tiene nombre o no tiene asignado un perfil',
            NotifyType.error,
            5000,
          )
        }
      } else {
        setLoading(false)
      }
    })
  }

  const handleCancelClick = () => {
    props.setShowForm(false)
  }

  const renderButtons = () => {
    return renderButtonsComponent(`plantillaData`, handleCancelClick)
  }

  const perfilBoxOptions = {
    items: perfilState.perfiles,
    value:
      perfilState.perfil?.Id != undefined
        ? perfilState.perfil?.Id
        : plantillaState.plantilla?.IdPerfil,
    placeholder: 'Seleccione un perfil',
    displayExpr: 'Nombre',
    valueExpr: 'Id',
    onValueChanged: (event: any) => {
      if (event.event) {
        dispatch(
          addPerfil(
            perfilState?.perfiles!.find((perfil) => perfil.Id === event.value),
          ),
        )
      }
    },
  }

  const handleUpdatedSujeto = (property: any) => {
    // dispatch(
    //   addSujeto({
    //     ...sujetoState,
    //     ...property,
    //   }),
    // )
  }
  //const formSujetoRef=useRef(null);
  return (
    <>
      <div className={'content-block  responsive-paddings'}>
        <div className={'content-block  '}>
          <h4>
            Plantilla de donde se hereda:{' '}
            {props['plantillaParent'] != undefined
              ? props['plantillaParent'].Nombre
              : ''}
          </h4>
          <form
            className={'legalForm'}
            onSubmit={handleSubmit(onSubmit)}
            encType="multipart/form-data"
            style={{ width: '100%' }}
          >
            <Form
              formData={formData.current}
              disabled={loading}
              //ref={formSujetoRef}
              readOnly={false}
              labelLocation={isLarge ? 'left' : 'top'}
              showValidationSummary={false}
              validationGroup="sujetoData"
              showColonAfterLabel={true}
            >
              <GroupItem colCount="2">
                <SimpleItem
                  dataField={'Nombre'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Nombre Plantillas',
                    mode: 'text',
                    value: plantillaState.plantilla?.Nombre
                      ? plantillaState.plantilla?.Nombre
                      : '',
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        dispatch(
                          addPlantilla({
                            ...plantillaState.plantilla,
                            Nombre: event.value,
                          }),
                        )
                      }
                    },
                  }}
                ></SimpleItem>
                <SimpleItem
                  dataField="Perfiles"
                  editorType="dxSelectBox"
                  editorOptions={perfilBoxOptions}
                ></SimpleItem>
              </GroupItem>

              <GroupItem colCount="1">
                <SimpleItem>
                  <PlantillaCampos></PlantillaCampos>
                </SimpleItem>
              </GroupItem>
              <SimpleItem render={renderButtons} cssClass="alignBottom" />
            </Form>
          </form>
        </div>
      </div>
    </>
  )
}

export default PlantillaFormPage
