import { ComponenteModel } from '../../models/componenteModel/componenteModel'

export interface ComponenteState {
  componente: ComponenteModel | undefined
  componentes: ComponenteModel[] | undefined
}

export const ADD_COMPONENTES = '@todo/ADD_COMPONENTES'
export const ADD_COMPONENTE = '@todo/ADD_COMPONENTE'
export const CLEAR_COMPONENTE = '@todo/CLEAR_COMPONENTE'

interface AddComponenteRequest {
  type: typeof ADD_COMPONENTE
  payload: Partial<ComponenteModel> | undefined
}
interface AddComponentesRequest {
  type: typeof ADD_COMPONENTES
  payload: ComponenteModel[] | undefined
}
interface ClearComponenteRequest {
  type: typeof CLEAR_COMPONENTE
  payload: ComponenteModel | undefined
}

export type ComponenteActionsTypes =
  | AddComponenteRequest
  | AddComponentesRequest
  | ClearComponenteRequest
