import React, { ReactElement, useEffect, useRef, useState } from 'react'
import Form, {
  ButtonItem,
  ButtonOptions,
  SimpleItem,
  Tab,
  TabbedItem,
  TabPanelOptions,
} from 'devextreme-react/form'

/* import './administracionTabsStyles.scss' */
import Box, { Item } from 'devextreme-react/box'
import { useHistory } from 'react-router-dom'
import CompanyFormPage from './CompanyFormPage'
import EnlacesFormComponent from '../components/enlacesFormComponent'
import CalculadoraFormComponent from '../components/calculadoraFormComponent'
import EmailFormComponent from '../components/emailFormComponent'
import { useDispatch, useSelector } from 'react-redux'
import { clearEmail } from '../redux/Email/emailActions'
import {
  addCalculadoraTranseferencias,
  clearCalculadoraTranseferencias,
} from '../redux/CalculadoraTransferencias/calculadoraTransferenciasActions'
import { addParentEnlaces, clearEnlace } from '../redux/Enlace/enlaceActions'
import {
  addCompany,
  addCompanyModulos,
  clearCompanyModulos,
} from '../redux/companiesActions'
import ModulosList from '../components/companyModuloComponent'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'
import { useScreenSize } from '../../../../utils/media-query'
import { RootState } from '../../../../redux/combineReducers'
import { useForm } from 'react-hook-form'
import { postCompany, putCompany } from '../services/companiesServices'
import { refreshGrid } from '../../../../utils/sharedUitls'
import { getEnlaces } from '../services/enlacesService'
import { CompanyModel } from '../models/companyModel'
import { CalculadoraTransferenciaModel } from '../models/calculadoraTransferenciaModel'
import {
  addHideLoader,
  addShowLoader,
} from '../../../../redux/actions/configActions'
import { custom } from 'devextreme/ui/dialog'

function CompanyFormTabsPage(props: any): ReactElement {
  const history = useHistory()
  const forceUpdate = React.useReducer(() => ({}), {})[1] as () => void
  const userState = useSelector((state: RootState) => state.user)
  const company = useSelector((state: RootState) => state.company?.company)
  const companyState = useSelector((state: RootState) => state.company)
  const calculadoraState = useSelector(
    (state: RootState) =>
      state.calculadoraTransferencias?.calculadoraTransferencias,
  )
  const emailState = useSelector((state: RootState) => state.email?.email)
  const enlaceState = useSelector((state: RootState) => state.enlace)
  const isChangedState = useSelector(
    (state: RootState) => state.sharedUtils.isChanged,
  )
  const { isLarge } = useScreenSize()
  const [loading, setLoading] = useState(false)
  const [pattern, setPattern] = useState('NIF')
  const formData = useRef({})
  const dispatch = useDispatch()
  const { register, handleSubmit } = useForm()
  const renderButtons = () => {
    return renderButtonsComponent(`companyData`, handleCancelClick)
  }
  const handleCancelClick = () => {
    let confirmDialog = custom({
      title: '',
      messageHtml:
        '<b>Para conservar los cambios antes pulsar Guardar, Si continua perderá todos los datos cambiados</b>',
      buttons: [
        {
          text: 'Continuar',
          onClick: (e) => {
            dispatch(addCompany(new CompanyModel()))
            props.setShowForm(false)
            confirmDialog.hide()
          },
        },
        {
          text: 'Cancelar',
          onClick: (e) => {
            props.setShowForm(true)
            confirmDialog.hide()
          },
        },
      ],
    })
    if (isChangedState) {
      confirmDialog.show().then((dialogResult: any) => {
        console.log(dialogResult)
      })
    }

    /*   dispatch(addCompany(new CompanyModel()))
    dispatch(addCalculadoraTranseferencias(new CalculadoraTransferenciaModel())) */
    dispatch(addCompanyModulos(undefined))

    props.setShowForm(false)
  }
  const onSubmit = async (data: any) => {
    setLoading(true)
    dispatch(addShowLoader(true))
    let result: boolean = false
    if (!company?.Id) {
      dispatch(addCompany({ ...company, Id: 0 }))
    }
    if (company?.Id) {
      result = await putCompany(
        userState!.user!,
        company,
        calculadoraState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    } else {
      result = await postCompany(
        userState!.user!,
        company!,
        calculadoraState,
        enlaceState?.enlaces!,
        companyState,
        emailState,
      )
    }

    if (result) {
      setLoading(false)
      refreshGrid(props, true)
      props.setShowForm(false)
      localStorage.setItem(
        'parentCompanyModulos',
        JSON.stringify(companyState?.companyModulos),
      )
      localStorage.setItem(
        'parentEnlaces',
        JSON.stringify(
          dispatch(
            addParentEnlaces(
              await getEnlaces(
                userState?.user!,
                parseInt(userState?.user!.CompanyId!),
              ),
            ),
          ).payload,
        ),
      )
    } else {
      setLoading(false)
      dispatch(addHideLoader(false))
    }
    dispatch(addHideLoader(false))
  }
  const companyFormRef: any = useRef(null)
  const companyTabRef: any = useRef(null)

  useEffect(() => {
    async function fetchData() {
      console.log('RENDER()')
    }
    fetchData()
  }, [])
  if (!props.showForm) {
    if (companyTabRef != null) {
    }
  }
  function selectedIndexClosed() {
    return props.showForm ? undefined : 1
  }

  return (
    <React.Fragment>
      <>
        <div className={'content-block responsive-paddings'} id={props.id}>
          <Box
            align="center"
            crossAlign="stretch"
            style={{ alignItems: 'center' }}
          >
            <Item ratio="1">
              <form
                className={'legalForm'}
                onSubmit={handleSubmit(onSubmit)}
                encType="multipart/form-data"
                style={{ width: '100%' }}
              >
                <Form ref={companyFormRef} id="tabs">
                  <TabbedItem
                    ref={companyTabRef}
                    //tabPanelOptions={{ deferRendering:true  }}
                  >
                    <Tab title="Datos principales">
                      <Box direction="col" width="100%">
                        <Item ratio="0" baseSize="120%">
                          <CompanyFormPage
                            row={props.rowSelected}
                            setShowForm={props.setShowForm}
                            showForm={props.showForm}
                            companyTabRef={companyFormRef}
                          ></CompanyFormPage>
                        </Item>
                      </Box>
                    </Tab>

                    <Tab title="Configuración Calculadora">
                      <Box direction="row" width="100%">
                        <Item ratio="0" baseSize="120%">
                          <CalculadoraFormComponent
                            row={props.rowSelected}
                            setShowForm={props.setShowForm}
                            showForm={props.showForm}
                            companyTabRef={companyFormRef}
                          ></CalculadoraFormComponent>
                        </Item>
                      </Box>
                    </Tab>
                    <Tab title="Enlaces">
                      <Box direction="row" width="100%">
                        <Item ratio="0" baseSize="120%">
                          <EnlacesFormComponent
                            row={props.rowSelected}
                            setShowForm={props.setShowForm}
                            showForm={props.showForm}
                            companyTabRef={companyFormRef}
                          ></EnlacesFormComponent>
                        </Item>
                      </Box>
                    </Tab>

                    <Tab title="Email">
                      <Box direction="row" width="100%">
                        <Item ratio="0" baseSize="120%">
                          <EmailFormComponent
                            row={props.rowSelected}
                            setShowForm={props.setShowForm}
                            showForm={props.showForm}
                            companyTabRef={companyTabRef}
                          ></EmailFormComponent>
                        </Item>
                      </Box>
                    </Tab>
                    <Tab title="Modulos">
                      <Box direction="row" width="100%">
                        <Item ratio="0" baseSize="120%">
                          <ModulosList
                            row={props.rowSelected}
                            setShowForm={props.setShowForm}
                            showForm={props.showForm}
                            companyTabRef={companyFormRef}
                          ></ModulosList>
                        </Item>
                      </Box>
                    </Tab>
                  </TabbedItem>

                  <SimpleItem render={renderButtons}></SimpleItem>
                </Form>
              </form>
            </Item>
          </Box>
        </div>
      </>
    </React.Fragment>
  )
}
export default CompanyFormTabsPage
