import { useState, ReactElement, useEffect, useRef } from 'react'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import ScrollView from 'devextreme-react/scroll-view'
import { SharedGrid } from 'react-library-grid'
import { RootState } from '../../../../redux/combineReducers'
import { addHideLoader } from '../../../../redux/actions/configActions'
import NotFound from '../../../../components/notFound/notFound'
import { getStructure, getPerfil, getPerfiles } from '../service/perfilService'
import { addPerfil } from '../redux/perfilActions'
import { ButtonColumnModel, DataGridModel } from 'shared-models'
import { Popup } from 'devextreme-react'
import PerfilFormPage from './perfilFormPage'
import { PerfilModel } from '../models/perfilModel'
import { useHistory } from 'react-router-dom'
import { PerfilesCampoDatoModel } from '../models/perfilCampoDatoModel'
import {
  addDataFormChange,
  isOpenPopup,
} from '../../../../redux/actions/sharedUtilsActions'
import {
  isAdministrador,
  isSuperAdministrador,
} from '../../../../utils/allowAuthUtil'
import {
  useScreenSize,
} from '../../../../utils/media-query'
import { GridHeight } from '../../../../utils/sharedUitls'

function PerfilPage(): ReactElement {
  const dispatch = useDispatch()
  const [structure, setStructure] = useState(new PerfilesCampoDatoModel())
  const userState = useSelector((state: RootState) => state.user)
  const perfilState = useSelector((state: RootState) => state.perfil)
  const [rowFocused, setRowFocused] = useState('')
  const [showForm, setShowForm] = useState(false)
  const [rowSelected, setRow] = useState(0)
  const history = useHistory()
  const popupPerfilsRef = useRef(null)
  const { isLarge, isMedium, isSmall, isXSmall } = useScreenSize()

  useEffect(() => {
    async function fetchData() {
      try {
        setStructure(await getStructure(userState!.user!))
        dispatch(addHideLoader(false))
      } catch (e) {
        console.error(e)
      }
    }
    fetchData()
  }, [])

  const handleOpenCreatePopup = async () => {
    if (
      isAdministrador(userState!.user!) ||
      isSuperAdministrador(userState!.user!)
    ) {
      dispatch(addPerfil(new PerfilModel()))
      dispatch(addDataFormChange(false))
      setShowForm(!showForm)
      dispatch(isOpenPopup(!showForm, popupPerfilsRef))
    }
  }

  const dataGridPerfilsRef = useRef(null)
  function fillDataGrid(): DataGridModel {
    const buttonColumn: ButtonColumnModel = new ButtonColumnModel({
      icon: 'edit',
      text: '',
      hint: 'Editar',
      cellRender: async (row: any) => {
        setRow(row.data)
        setShowForm(!showForm)
        const perfil: PerfilModel = await getPerfil(
          userState!.user!,
          row.data.Id,
        )
        dispatch(addPerfil(await getPerfil(userState!.user!, row.data.Id)))
      },
    })
    let gridheight = '600;'
    gridheight = isXSmall ? '300' : '600'
    gridheight = isSmall ? '400' : '600'
    gridheight = isMedium ? '600' : '600'
    gridheight = isLarge ? '700' : '600'
    let dataGridModel = new DataGridModel({
      dataGridName: 'perfilGridComponent',
      height: gridheight,
      ref: dataGridPerfilsRef,
      rowFocused: rowFocused,
      columns: structure.Campos,
      openRow: ({}) => {},
      openRowEnterKey: ({}) => {},
      focusEventChanged: (e: any) => {
        if (e.row) {
          setRowFocused(e.row.key)
        }
      },
      focusEventChanging: ({}) => {},
      openCreatePopup: handleOpenCreatePopup,
      user: userState!.user!,
      url: `${process.env.REACT_APP_API_URL_CORE}/Perfil/Grid/FilterGroup`,
      urlAux: `${process.env.REACT_APP_API_URL_CORE}/Perfil/Grid`,
      buttonsColumns: [buttonColumn],
      filterRow: true,
      filterPanel: true,
      columnChooser: true,
      headerFilter: false,
      exportData: false,
      showSearchPanel: true,
      showToolbar: true,
      history: history,
      singularName: 'Perfil',
      pluralName: 'Perfiles',
      pageName: 'Perfil',
      openedPopup: showForm,
    })

    return dataGridModel
  }

  return (
    <>
      {/* {isAllowed(PermisoType.AccesoAdministracion, userState!.user) ? ( */}
      {true ? (
        <div
          className={'content-block dx-card'}
          style={{
            borderColor: '#f4f9ff',
            borderWidth: '2px',
            margin: '0.3%',
            padding: '0.16%',
          }}
        >
          <h3 style={{ margin: 0, padding: 0 }}>Perfiles</h3>
          {structure?.Campos != undefined && structure?.Campos.length > 0 ? (
            <ScrollView direction="vertical">
              <div style={{ height: '75vh' }}>
                <SharedGrid dataGridModel={fillDataGrid()} />
              </div>
            </ScrollView>
          ) : null}
          <Popup
            visible={showForm}
            ref={popupPerfilsRef}
            onHiding={({}) => {
              setShowForm(!showForm)
              dispatch(isOpenPopup(false, popupPerfilsRef))
            }}
            dragEnabled={true}
            closeOnOutsideClick={false}
            showTitle={true}
            title="Formulario Perfil"
            width={'60%'}
            height={'50%'}
          >
            <PerfilFormPage
              row={rowSelected}
              setShowForm={setShowForm}
              showForm={showForm}
            ></PerfilFormPage>
          </Popup>
        </div>
      ) : (
        <div className={'content-block dx-card'}>
          <NotFound data={{ type: 1 }}></NotFound>
        </div>
      )}
      {}
    </>
  )
}

export default PerfilPage
