import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Form } from 'devextreme-react'
import { GroupItem, RequiredRule, SimpleItem } from 'devextreme-react/form'
import { confirm, custom } from 'devextreme/ui/dialog'
import { useForm } from 'react-hook-form'
import { RootState } from '../../../../redux/combineReducers'
import { useScreenSize } from '../../../../utils/media-query'
import { postPerfil, putPerfil } from '../service/perfilService'
import { addPerfil } from '../redux/perfilActions'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'

function PerfilFormPage(props: any): ReactElement {
  const userState = useSelector((state: RootState) => state.user)
  const perfilState = useSelector((state: RootState) => state.perfil)
  const { isXSmall, isLarge } = useScreenSize()
  const [loading, setLoading] = useState(false)
  const formData = useRef({})
  const { register, handleSubmit } = useForm()
  const dispatch = useDispatch()
  useEffect(() => {
    async function fetchData() {}
    fetchData()
  }, [])

  const onSubmit = async (data: any) => {
    setLoading(true)

    let result: boolean = false
    if (!perfilState?.perfil?.Id) {
      dispatch(addPerfil({ ...perfilState, Id: 0 }))
    }

    if (perfilState?.perfil?.Id) {
      result = await putPerfil(userState!.user!, perfilState.perfil)
    } else {
      result = await postPerfil(userState!.user!, perfilState.perfil!)
    }

    if (result) {
      setLoading(false)
      const refreshButton = document.getElementsByClassName(
        'dx-icon dx-icon-refresh',
      )[0] as HTMLElement
      refreshButton.click()
      props.setShowForm(false)
    } else {
      setLoading(false)
    }
  }

  const handleCancelClick = () => {
    // let confirmDialog = custom({
    //   title: '',
    //   messageHtml:
    //     '<b>Para conservar los cambios antes pulsar Guardar, Si continua perderá todos los datos cambiados</b>',
    //   buttons: [
    //     {
    //       text: 'Continuar',
    //       onClick: (e) => {
    //         props.setShowForm(false)
    //         confirmDialog.hide()
    //       },
    //     },
    //     {
    //       text: 'Cancelar',
    //       onClick: (e) => {
    //         confirmDialog.hide()
    //       },
    //     },
    //   ],
    // })
    // confirmDialog.show().then((dialogResult: any) => {
    //   console.log(dialogResult)
    // })
    props.setShowForm(false)
  }

  const renderButtons = () => {
    if (!props.showForm) {
      perfilFormRef.current.instance.resetValues()
    }
    return renderButtonsComponent(`perfilData`, handleCancelClick)
  }

  const handleUpdatedPerfil = (property: any) => {
    const perfil = perfilState.perfil
    dispatch(
      addPerfil({
        ...perfil,
        ...property,
      }),
    )
  }
  const perfilFormRef: any = useRef(null)
  return (
    <>
      <div className={'content-block  responsive-paddings'}>
        <div className={'content-block  '}>
          <form
            className={'legalForm'}
            onSubmit={handleSubmit(onSubmit)}
            encType="multipart/form-data"
            style={{ width: '100%' }}
          >
            <Form
              ref={perfilFormRef}
              formData={formData.current}
              disabled={loading}
              readOnly={false}
              labelLocation={isLarge ? 'left' : 'top'}
              showValidationSummary={false}
              validationGroup="perfilData"
              showColonAfterLabel={true}
            >
              <GroupItem colCount="2">
                <SimpleItem
                  dataField="Nombre"
                  helpText=""
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Nombre',
                    mode: 'text',
                    value: perfilState?.perfil?.Nombre,
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedPerfil({ Nombre: event.value })
                      }
                    },
                  }}
                >
                  <RequiredRule message="Debes introducir un nombre" />
                </SimpleItem>
                <SimpleItem
                  dataField={'Descripción'}
                  editorType={'dxTextArea'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Descripción',
                    mode: 'text',
                    value: perfilState?.perfil?.Descripcion
                      ? perfilState.perfil.Descripcion
                      : null,
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedPerfil({ Descripcion: event.value })
                      }
                    },
                  }}
                ></SimpleItem>
              </GroupItem>
              <SimpleItem render={renderButtons} cssClass="alignBottom" />
            </Form>
          </form>
        </div>
      </div>
    </>
  )
}

export default PerfilFormPage
