import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'
import {
  checkStatusCode,
  customLog,
  logType,
  methodsEndPointType,
} from 'shared-utils'
import { logger } from 'login-service'
import { authHeader } from 'shared-services'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { ConfiguracionEnvioEmailModel } from '../models/configuracionEnvioEmailModel'
import { UserModel } from 'shared-models'

const serviceName = 'EmailService'
export const getEmail = async (
  user: UserModel,
  idCompany: Number,
): Promise<ConfiguracionEnvioEmailModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url =
      `${process.env.REACT_APP_API_URL_CORE}/ConfiguracionEnvioEmail/GetByCompany/` +
      idCompany
    let EmailData: ConfiguracionEnvioEmailModel =
      new ConfiguracionEnvioEmailModel()
    await axios({
      url: url,
      method: 'GET',
      headers: authHeader(user!),
    })
      .then((response) => {
        EmailData = jsonConvert.deserializeObject(
          response.data,
          ConfiguracionEnvioEmailModel,
        )
      })
      .catch((error) => {
        console.log('error: ', error)
        showToast(
          'No se han podido obtener la configuración del Email.',
          NotifyType.error,
          5000,
        )
      })

    return EmailData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}

export const postEmail = async (
  user: UserModel,
  email: ConfiguracionEnvioEmailModel | undefined,
  idCompany: Number,
): Promise<ConfiguracionEnvioEmailModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/ConfiguracionEnvioEmail`
    let EmailData: ConfiguracionEnvioEmailModel =
      new ConfiguracionEnvioEmailModel()
    await axios({
      url: url,
      method: 'POST',
      headers: authHeader(user!),
      data: {
        IdCompany: idCompany,
        ServidorSMTP: email?.ServidorSMTP,
        PuertoSMTP: email?.PuertoSMTP,
        UsaSSL: email?.UsaSSL,
        NombreMostrar: email?.NombreMostrar,
        DireccionEmail: email?.DireccionEmail,
        UsuarioEmail: email?.UsuarioEmail,
        PasswordEmail: email?.PasswordEmail,
      },
    })
      .then((response) => {
        console.log('Este es el Codigo de respuesta' + response.status)
        checkStatusCode(response.status)
        EmailData = jsonConvert.deserializeObject(
          response.data,
          ConfiguracionEnvioEmailModel,
        )
        // showToast(
        //   'Configuración Guardada correctamente!.',
        //   NotifyType.success,
        //   5000,
        // )
      })
      .catch((error) => {
        /*   customLog(
          `Error al crear Calculadora => ${error.response.data}`,
          logType.ERROR,
          serviceName,
          postEmail.name,
          methodsEndPointType.POST,
        ) */
        showToast(
          'No se han podido Guardar la configuración del Email.',
          NotifyType.error,
          5000,
        )
      })

    return EmailData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}

export const putEmail = async (
  user: UserModel,
  email: ConfiguracionEnvioEmailModel | undefined,
  idCompany: Number,
): Promise<ConfiguracionEnvioEmailModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/ConfiguracionEnvioEmail`
    let EmailData: ConfiguracionEnvioEmailModel =
      new ConfiguracionEnvioEmailModel()

    await axios
      .put(url, email, { headers: authHeader(user!) })
      .then(async (response) => {
        checkStatusCode(response.status)
        customLog(
          `Configuración de email editada =>     ${response.data.Id}`,
          logType.ERROR,
          serviceName,
          putEmail.name,
          methodsEndPointType.PUT,
        )
        // showToast('Configuración de email editada', NotifyType.success, 5000)
      })
      .catch((error) => {
        customLog(
          `Error al guardar ConfiguracionEnvioEmail => ${error.message}`,
          logType.ERROR,
          serviceName,
          putEmail.name,
          methodsEndPointType.PUT,
        )
      })

    return EmailData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
