import { combineReducers } from 'redux'
import { configReducer } from './reducers/configReducers'
import { expedienteReducer } from '../pages/expedientePage/redux/expedienteReducer'
import { administracionLegalReducer } from '../pages/administracionPages/legalAdministracion/redux/administracionLegalReducer'

import {
  userReducer,
  userResetReducer,
} from '../pages/loginPage/redux/authReducer'
import { sharedUtilsReducer } from './reducers/sharedUtilsReducer'
import { administracionEmailReducer } from '../pages/administracionPages/emailAdministracion/redux/administracionEmailReducer'
import { sujetoReducer } from '../pages/companyAdministracionPages/sujetoPage/redux/sujetoReducer'
import { companyReducer } from '../pages/companyAdministracionPages/companyPage/redux/companiesReducer'
import { moduloReducer } from '../pages/companyAdministracionPages/plantillasAdministracion/redux/modulo/moduloReducer'
import { componenteReducer } from '../pages/companyAdministracionPages/plantillasAdministracion/redux/componente/componenteReducer'
import { usuarioReducer } from '../pages/usuarioAdministracionPages/usuarioPage/redux/usuarioReducer'
import { datoUsuarioReducer } from '../pages/usuarioAdministracionPages/datoUsuarioPage/redux/datoUsuarioReducer'
import { moduloComponenteReducer } from '../pages/companyAdministracionPages/plantillasAdministracion/redux/moduloComponente/moduloComponenteReducer'
import { calculadoraTransferenciasReducer } from '../pages/companyAdministracionPages/companyPage/redux/CalculadoraTransferencias/calculadoraTransferenciasReducer'
import { emailReducer } from '../pages/companyAdministracionPages/companyPage/redux/Email/emailReducer'
import { enlaceReducer } from '../pages/companyAdministracionPages/companyPage/redux/Enlace/enlaceReducer'
import { perfilReducer } from '../pages/administracionPages/perfilPage/redux/perfilReducer'
import { plantillaReducer } from '../pages/companyAdministracionPages/plantillasAdministracion/redux/plantilla/plantillaComponenteReducer'

const rootReducer = combineReducers({
  expediente: expedienteReducer,
  user: userReducer,
  config: configReducer,
  resetPasswd: userResetReducer,
  administracionLegal: administracionLegalReducer,
  administracionEmail: administracionEmailReducer,
  sharedUtils: sharedUtilsReducer,
  sujeto: sujetoReducer,
  company: companyReducer,
  modulo: moduloReducer,
  componente: componenteReducer,
  usuario: usuarioReducer,
  datoUsuario: datoUsuarioReducer,
  moduloComponente: moduloComponenteReducer,
  calculadoraTransferencias: calculadoraTransferenciasReducer,
  email: emailReducer,
  enlace: enlaceReducer,
  perfil: perfilReducer,
  plantilla: plantillaReducer,
})

export type RootState = ReturnType<typeof rootReducer>
export default rootReducer
