import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Button, FileUploader, Form, ProgressBar } from 'devextreme-react'

import {
  GroupItem,
  Item,
  Label,
  RequiredRule,
  SimpleItem,
} from 'devextreme-react/form'
import './scss/companiesAdministracionStyles.scss'

import { checkScreenSize } from 'shared-utils'
import { confirm } from 'devextreme/ui/dialog'

import ReactDOM from 'react-dom'
import CreateToolbarComponent from '../../../administracionPages/navigationAdministracion/toolbarComponent'

import { useForm } from 'react-hook-form'
import { RootState } from '../../../../redux/combineReducers'
import { useScreenSize } from '../../../../utils/media-query'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { ToolbarModel } from '../../../../models/toolbarModel'
import { postCompany } from '../services/companiesServices'
import { CompanyModel } from '../models/companyModel'

function CompaniesAdministracionComponent(): ReactElement {
  const userState = useSelector((state: RootState) => state.user)
  const administracionLegalState = useSelector(
    (state: RootState) => state.administracionLegal?.legal,
  )
  const { isXSmall, isLarge } = useScreenSize()

  const [loading, setLoading] = useState(false)
  const formData = useRef({})
  const dispatch = useDispatch()
  var principalLogo: any
  var secondaryLogo: any

  const [isDropZoneActiveLogo1, setIsDropZoneActiveLogo1] = useState(false)
  const [isDropZoneActiveLogo2, setIsDropZoneActiveLogo2] = useState(false)
  const [imageSourceLogo1, setImageSourceLogo1]: any = useState('')
  const [imageSourceLogo2, setImageSourceLogo2]: any = useState('')
  const [textVisibleLogo1, setTextVisibleLogo1] = useState(true)
  const [textVisibleLogo2, setTextVisibleLogo2] = useState(true)
  const [progressVisibleLogo1, setProgressVisibleLogo1] = useState(false)
  const [progressVisibleLogo2, setProgressVisibleLogo2] = useState(false)
  const [progressValueLogo1, setProgressValueLogo1] = useState(0)
  const [progressValueLogo2, setProgressValueLogo2] = useState(0)

  const { register, handleSubmit } = useForm()

  useEffect(() => {
    async function fetchData() {}
    fetchData()
  }, [])

  const onSubmit = async (data: any) => {
    const {
      allowChangePassword,
      contactInformation,
      enabled,
      name,
      notificationsEmail,
      principalLogo,
      secondaryLogo,
      slogan,
    } = formData.current as any
    setLoading(true)

    let confirmDialog = confirm(
      '<i>¿Está seguro de que quiere guardar la información?</i>',
      'Confirma los cambios',
    )
    confirmDialog.then((dialogResult) => {
      if (dialogResult) {
        dialogCall()
      } else {
        setLoading(false)
      }
    })

    const dialogCall = async () => {
      //const result = await postCompany(formData.current)
      const result = await postCompany(userState!.user!, new CompanyModel())

      if (result) {
        setLoading(false)
        //? para Agregar a redux
        //dispatch(addLegalAdminstracion(result));
        //notify(result.message, "error", 2000);
      } else {
        setLoading(false)
      }
    }
  }
  //gestion de subida de imagenes
  const onSelectedFilesChanged = (selectorFiles: FileList) => {
    principalLogo = selectorFiles
  }
  const onSelectedFilesChanged2 = (selectorFiles2: FileList) => {
    secondaryLogo = selectorFiles2
  }
  const onDropZoneEnterLogo1 = (e: any) => {
    if (e.dropZoneElement.id === 'dropzone-external') {
      setIsDropZoneActiveLogo1(true)
    }
  }

  const onDropZoneLeaveLogo1 = (e: any) => {
    if (e.dropZoneElement.id === 'dropzone-external') {
      setIsDropZoneActiveLogo1(false)
    }
  }
  const onDropZoneEnterLogo2 = (e: any) => {
    if (e.dropZoneElement.id === 'dropzone-external2') {
      setIsDropZoneActiveLogo2(true)
    }
  }

  const onDropZoneLeaveLogo2 = (e: any) => {
    if (e.dropZoneElement.id === 'dropzone-external2') {
      setIsDropZoneActiveLogo2(false)
    }
  }
  const onProgressLogo1 = (e: any) => {
    setProgressValueLogo1((e.bytesLoaded / e.bytesTotal) * 100)
  }
  const onProgressLogo2 = (e: any) => {
    setProgressValueLogo2((e.bytesLoaded / e.bytesTotal) * 100)
  }

  const onUploadStartedLogo1 = () => {
    setImageSourceLogo1('')
    setProgressVisibleLogo1(true)
  }

  const onUploadStartedLogo2 = () => {
    setImageSourceLogo2('')
    setProgressVisibleLogo2(true)
  }
  const onUploadedLogo1 = (e: any) => {
    const file = e.file
    const fileReader = new FileReader()
    fileReader.onload = () => {
      setIsDropZoneActiveLogo1(false)
      setImageSourceLogo1(fileReader.result)
    }
    fileReader.readAsDataURL(file)
    setTextVisibleLogo1(false)
    setProgressVisibleLogo1(false)
    setProgressValueLogo1(0)
  }
  const onUploadedLogo2 = (e: any) => {
    const file = e.file
    const fileReader = new FileReader()
    fileReader.onload = () => {
      setIsDropZoneActiveLogo2(false)
      setImageSourceLogo2(fileReader.result)
    }
    fileReader.readAsDataURL(file)
    setTextVisibleLogo2(false)
    setProgressVisibleLogo2(false)
    setProgressValueLogo2(0)
  }
  //-----final gestion subida de imagenes

  //render para botones de guardar y concelar
  const renderButtons = () => {
    return (
      <>
        <Button
          icon="remove"
          text="Cancelar"
          type="normal"
          stylingMode="outlined"
          style={{
            float: 'right',
            margin: '1em',
          }}
        />

        <Button
          icon="save"
          text="Guardar"
          type="default"
          useSubmitBehavior={true}
          stylingMode="contained"
          style={{ float: 'right', margin: '1em' }}
        />

        {/* //! spinner de los botones, hay que ver como meterlo 
         <span className="dx-button-text">
          {loading ? (
            <LoadIndicator width={"24px"} height={"24px"} visible={true} />
          ) : (
            <>
              {" "}
              <i className="dx-icon-remove"> </i>
              Cancelar
            </>
          )}
        </span> */}
      </>
    )
  }
  //-----final render botones de guardar y cancelar
  return (
    <>
      <div
        className={'content-block dx-card responsive-paddings'}
        style={{ borderColor: '#f4f9ff', borderWidth: '2px', margin: '0.6%' }}
      >
        <div className={'content-block  '}>
          <form
            className={'legalForm'}
            onSubmit={handleSubmit(onSubmit)}
            encType="multipart/form-data"
            style={{ width: '100%' }}
          >
            <Form
              formData={formData.current}
              disabled={loading}
              labelLocation={isLarge ? 'left' : 'top'}
            >
              <GroupItem
                caption="Creación de Compañía"
                cssClass={'titleCenter'}
              ></GroupItem>
              <GroupItem colCount="2">
                <Item
                  dataField={'name'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Nombre de compañía',

                    mode: 'text',
                  }}
                >
                  <RequiredRule message="El campo es requerido" />

                  <Label visible={true} text="Nombre" />
                </Item>
                <Item
                  dataField={'slogan'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Eslogan de la compañía',

                    mode: 'text',
                  }}
                >
                  <RequiredRule message="El campo es requerido" />

                  <Label visible={true} text="Eslogan" />
                </Item>
                <Item
                  dataField={'contactInformation'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Información de contacto',

                    mode: 'text',
                  }}
                >
                  <RequiredRule message="El campo es requerido" />

                  <Label visible={true} text="Información del contacto" />
                </Item>

                <Item
                  dataField={'notificationsEmail'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Email para notificaciones',

                    mode: 'text',
                  }}
                >
                  <RequiredRule message="El campo es requerido" />
                  {/* <EmailRule message="Email is invalid" /> */}
                  <Label visible={true} text="Email para notificaciones" />
                </Item>
                <Item
                  dataField={'allowChangePassword'}
                  editorType={'dxCheckBox'}
                  editorOptions={{
                    stylingMode: 'filled',

                    mode: 'text',
                  }}
                >
                  <Label visible={true} text="Permitir cambiar contraseña" />
                </Item>
                <Item
                  dataField={'enabled'}
                  editorType={'dxCheckBox'}
                  editorOptions={{
                    stylingMode: 'filled',

                    mode: 'text',
                  }}
                >
                  <Label visible={true} text="Compañía activa" />
                </Item>
              </GroupItem>
              <GroupItem colCount="2">
                <Item dataField={'principalLogo'}>
                  <div className="widget-container flex-box">
                    <span>Logo principal</span>
                    <div
                      id="dropzone-external"
                      className={`flex-box ${
                        isDropZoneActiveLogo1
                          ? 'dx-theme-accent-as-border-color dropzone-active'
                          : 'dx-theme-border-color'
                      }`}
                    >
                      {imageSourceLogo1 && (
                        <img
                          id="dropzone-image"
                          src={imageSourceLogo1}
                          alt=""
                        />
                      )}
                      {textVisibleLogo1 && (
                        <div id="dropzone-text" className="flex-box">
                          <span>Arrastra aquí el archivo</span>
                          <span>…o haz clic para subirlo.</span>
                        </div>
                      )}
                      <ProgressBar
                        id="upload-progress"
                        min={0}
                        max={100}
                        width="30%"
                        showStatus={false}
                        visible={progressVisibleLogo1}
                        value={progressValueLogo1}
                      ></ProgressBar>
                    </div>
                    <FileUploader
                      id="file-uploader"
                      dialogTrigger="#dropzone-external"
                      dropZone="#dropzone-external"
                      multiple={false}
                      allowedFileExtensions={['.jpg', '.jpeg', '.gif', '.png']}
                      uploadMode="instantly"
                      uploadUrl="https://js.devexpress.com/Demos/NetCore/FileUploader/Upload"
                      visible={false}
                      onDropZoneEnter={onDropZoneEnterLogo1}
                      onDropZoneLeave={onDropZoneLeaveLogo1}
                      onUploaded={onUploadedLogo1}
                      onProgress={onProgressLogo1}
                      onUploadStarted={onUploadStartedLogo1}
                      onValueChange={(e) => {
                        onSelectedFilesChanged(e)
                      }}
                    ></FileUploader>
                  </div>
                  <Label visible={false} text="Logo principal" />
                </Item>

                <Item dataField={'secondaryLogo'}>
                  <div className="widget-container flex-box">
                    <span>Logo Secundario</span>
                    <div
                      id="dropzone-external2"
                      className={`flex-box ${
                        isDropZoneActiveLogo2
                          ? 'dx-theme-accent-as-border-color dropzone-active'
                          : 'dx-theme-border-color'
                      }`}
                    >
                      {imageSourceLogo2 && (
                        <img
                          id="dropzone-image2"
                          src={imageSourceLogo2}
                          alt=""
                        />
                      )}
                      {textVisibleLogo2 && (
                        <div id="dropzone-text2" className="flex-box">
                          <span>Arrastra aquí el archivo</span>
                          <span>…o haz clic para subirlo.</span>
                        </div>
                      )}
                      <ProgressBar
                        id="upload-progress2"
                        min={0}
                        max={100}
                        width="30%"
                        showStatus={false}
                        visible={progressVisibleLogo2}
                        value={progressValueLogo2}
                      ></ProgressBar>
                    </div>
                    <FileUploader
                      id="file-uploader2"
                      dialogTrigger="#dropzone-external2"
                      dropZone="#dropzone-external2"
                      multiple={false}
                      allowedFileExtensions={['.jpg', '.jpeg', '.gif', '.png']}
                      uploadMode="instantly"
                      uploadUrl="https://js.devexpress.com/Demos/NetCore/FileUploader/Upload"
                      visible={false}
                      onDropZoneEnter={onDropZoneEnterLogo2}
                      onDropZoneLeave={onDropZoneLeaveLogo2}
                      onUploaded={onUploadedLogo2}
                      onProgress={onProgressLogo2}
                      onUploadStarted={onUploadStartedLogo2}
                      onValueChange={(e) => {
                        onSelectedFilesChanged2(e)
                      }}
                    ></FileUploader>
                  </div>
                  <Label visible={false} text="Logo Secundario" />
                </Item>
              </GroupItem>
              <SimpleItem render={renderButtons} cssClass="alignBottom" />
            </Form>
          </form>
        </div>
      </div>
    </>
  )
}

export default CompaniesAdministracionComponent
