import { ADD_MODULO_COMPONENTE, ADD_MODULO_COMPONENTES, CLEAR_MODULO_COMPONENTE, ModuloComponenteActionsTypes, ModuloComponenteState } from "./moduloComponenteType";



 const initialState: ModuloComponenteState = {

  moduloComponente: undefined,
  moduloComponentes:undefined,

}; 

 export const moduloComponenteReducer = (
  state = initialState,
  action: ModuloComponenteActionsTypes
): ModuloComponenteState => {
  switch (action.type) {
    
    case ADD_MODULO_COMPONENTE: {
      return { ...state, moduloComponente: action.payload };
    }
    case ADD_MODULO_COMPONENTES: {
         return { ...state, moduloComponentes: action.payload };
     }
    case CLEAR_MODULO_COMPONENTE: {
      return { ...state, moduloComponente: action.payload };
    }
    default:
      return state;
  }
}; 
