import { ADD_MODULO, ADD_MODULOS, CLEAR_MODULO, ModuloActionsTypes, ModuloState } from "./moduloType";


 const initialState: ModuloState = {

  modulo: undefined,
  modulos:undefined,

}; 

 export const moduloReducer = (
  state = initialState,
  action: ModuloActionsTypes
): ModuloState => {
  switch (action.type) {
    
    case ADD_MODULO: {
      return { ...state, modulo: action.payload };
    }
    case ADD_MODULOS: {
         return { ...state, modulos: action.payload };
     }
    case CLEAR_MODULO: {
      return { ...state, modulo: action.payload };
    }
    default:
      return state;
  }
}; 
