import { JsonObject, JsonProperty } from 'json2typescript'

@JsonObject('CompanyModuloModel')
export class CompanyModuloModel {
  @JsonProperty('IdCompany', Number)
  IdCompany: number = 0

  @JsonProperty('IdModulo', Number)
  IdModulo: number = 0

  @JsonProperty('Nombre', String)
  Nombre: String = ''
}
