import React, { ReactElement, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Form, TagBox } from 'devextreme-react'
import {
  EmailRule,
  GroupItem,
  Label,
  PatternRule,
  RequiredRule,
  SimpleItem,
} from 'devextreme-react/form'
import { custom } from 'devextreme/ui/dialog'
import { useForm } from 'react-hook-form'
import { RootState } from '../../../../redux/combineReducers'
import { useScreenSize } from '../../../../utils/media-query'
import { renderButtonsComponent } from '../../../../components/buttonsForm/renderButtons'
import { addUsuario, clearUsuario } from '../redux/usuarioActions'
import { postUsuario, putUsuario } from '../services/usuarioServices'
import { addDataFormChange } from '../../../../redux/actions/sharedUtilsActions'
import { refreshGrid, rolesName } from '../../../../utils/sharedUitls'
import {
  isAdministrador,
  isNormalUserByIdRole,
  isSuperAdministrador,
} from '../../../../utils/allowAuthUtil'
import { RoleModel } from '../../../../models/roleModel'
import { SujetoModel } from '../../../companyAdministracionPages/sujetoPage/models/sujetoModel'
import { UsuarioModel } from '../models/usuarioModel'
import { addSujetos } from '../../../companyAdministracionPages/sujetoPage/redux/sujetoActions'
import { getSujetos } from '../../../companyAdministracionPages/sujetoPage/service/sujetoService'

function UsuarioFormPage(props: any): ReactElement {
  const userState = useSelector((state: RootState) => state.user)
  const usuarioState = useSelector((state: RootState) => state.usuario?.usuario)
  const roleState = useSelector((state: RootState) => state.usuario?.roles)
  const sujetoState = useSelector((state: RootState) => state.sujeto)
  const isChangedState = useSelector(
    (state: RootState) => state.sharedUtils.isChanged,
  )
  const { isXSmall, isLarge } = useScreenSize()
  const [loading, setLoading] = useState(false)
  const [isFirtsTimeShowForm, setFirtsTimeShowForm] = useState(false)
  const [isSaveForm, setSaveForm] = useState(false)
  const [updatedForm, setUpdatedForm] = useState(false)
  const [isCuenta, setIsCuenta] = useState(
    usuarioState?.IdRole == 99 ? true : false,
  )

  const perfilState = useSelector((state: RootState) => state.perfil)
  const formData = useRef({})
  const { register, handleSubmit } = useForm()
  const dispatch = useDispatch()
  //console.log(isCuenta)
  const [showFormRolesPermisos, setShowFormRolesPermisos] = useState(false)
  const diff = require('recursive-diff')
  const [readOnlyTipoUsuario, setReadOnlyTipoUsuario] = useState(false)
  useEffect(() => {
    let isMounted = true
    async function fetchData() {}
    fetchData()
    return () => {
      isMounted = false
    }
  }, [props.showForm])

  const onSubmit = async (formData: any) => {
    if (usuarioState?.Id) {
      await putUsuario(userState!.user!, usuarioState)
      refreshGrid(props, true)
      //dispatch(addSujetos(await getSujetos(userState!.user!)))
      props.setShowForm(false)
      setUpdatedForm(false)
      dispatch(
        addUsuario({
          ...new UsuarioModel(),
          IdRole: -1,
          IdSujeto: sujetoState?.sujetos?.Datos[0].Id,
          RolesTipoCuenta: [],
        }),
      )
    } else {
      const usuario = await postUsuario(userState!.user!, usuarioState!)
      if (usuario) {
        dispatch(
          addUsuario({
            ...new UsuarioModel(),
            IdRole: -1,
            IdSujeto: sujetoState?.sujetos?.Datos[0].Id,
            RolesTipoCuenta: [],
          }),
        )
        refreshGrid(props, true)

        dispatch(addSujetos(await getSujetos(userState!.user!)))
        setSaveForm(false)
        setUpdatedForm(false)
      } else {
        setSaveForm(false)
      }
    }
  }

  const handleCancelClick = () => {
    if (updatedForm) {
      const buttonAttributes = {
        id: 'usuarioCancelButton',
        class: 'usuarioCancelButtonClass',
      }
      let confirmDialog = custom({
        title: '',

        messageHtml:
          '<b>Para conservar los cambios antes pulsar Guardar, Si continua perderá todos los datos cambiados</b>',
        buttons: [
          {
            text: 'Continuar',
            onClick: (e) => {
              props.setShowForm(false)
              setIsCuenta(false)
              dispatch(clearUsuario(new UsuarioModel()))
              setSaveForm(false)
              setUpdatedForm(false)
              confirmDialog.hide()
            },
          },
          {
            elementAttr: { buttonAttributes },
            text: 'Cancelar',

            onClick: (e) => {
              confirmDialog.hide()
            },
          },
        ],
      })
      confirmDialog.show().then((dialogResult: any) => {})
    } else {
      props.setShowForm(false)
      setIsCuenta(false)
      dispatch(clearUsuario(new UsuarioModel()))
      setSaveForm(false)
      setUpdatedForm(false)
    }
  }

  const renderButtons = () => {
    if (!props.showForm) {
      usuarioFormRef.current.instance.resetValues()
      if (usuarioState?.Id == 0) {
        setIsCuenta(true)
      } else {
        setIsCuenta(false)
      }
      if (usuarioState?.IdTipoUsuario == 3) {
        setIsCuenta(true)
      }
    }

    return renderButtonsComponent(`usuarioData`, handleCancelClick)
  }

  const getSujetosForm = (
    sujetosState: Partial<SujetoModel[]> | undefined,
    readAll: Boolean = false,
  ) => {
    let sujetos: Partial<Partial<SujetoModel>[]> = []
    if (sujetosState) {
      if (readAll) {
        sujetos?.push({ Id: 0, Nombre: `No selección` })
        sujetoState.sujetos?.Datos.map((x) => {
          return sujetos?.push({ Id: x.Id, Nombre: x.Nombre })
        })
        return sujetos
      } else {
        if (isAdministrador(userState!.user!)) {
          sujetos = sujetosState!.filter(
            (x) => x?.Id == parseInt(userState!.user!.SujetoId),
          )
        } else return sujetoState.sujetos?.Datos
      }

      return sujetos.map((x) => {
        return { Id: x?.Id, Nombre: x?.Nombre }
      })
    } else return []
  }

  //Select para los sujetos
  const sujetoSelectedBoxOptions = {
    items: getSujetosForm(sujetoState?.sujetos?.Datos, true),
    value: usuarioState?.IdSujeto,
    placeholder: 'Seleccione una compañía',
    displayExpr: 'Nombre',
    valueExpr: 'Id',
    onValueChanged: (event: any) => {
      if (props.showForm) {
        if (event.previousValue != event.value && event.previousValue != null) {
          if (event.value != 0) {
            handleUpdatedUsuario({
              IdSujeto: event.value,
              SujetoIndetification: '',
            })
          } else if (event.event) {
            handleUpdatedUsuario({ IdSujeto: event.value })
          }
        }
      }
    },
  }
  const getSujetoList = () => {
    let id: Number | undefined = 0
    if (isAdministrador(userState!.user!)) {
      id = parseInt(userState!.user!.SujetoId)
    } else if (isSuperAdministrador(userState!.user!)) {
      id = usuarioState?.IdSujeto
    } else id = parseInt(userState!.user!.SujetoId)

    if (id == 0) {
      return sujetoState?.sujetos?.Datos[0].Id
    } else return id
  }
  //Select para los gestorias
  const gestoriasSelectedBoxOptions = {
    items: getSujetosForm(sujetoState?.sujetos?.Datos),
    value: getSujetoList(),
    placeholder: 'Seleccione una compañía',
    displayExpr: 'Nombre',
    valueExpr: 'Id',
    readOnly: isAdministrador(userState!.user!) ? true : false,
    disabled: usuarioState?.Id ? true : false, //deshabilito cuando hay usuario cargado
    onValueChanged: (event: any) => {
      if (props.showForm) {
        if (event.previousValue != event.value && event.previousValue != null) {
          if (event.value != 0) {
            handleUpdatedUsuario({
              IdSujeto: event.value,
              SujetoIndetification: '',
            })
          } else if (event.event) {
            handleUpdatedUsuario({
              IdSujeto: sujetoState?.sujetos?.Datos[0].Id,
            })
          }
        }
      }
    },
  }
  const getRolesTipoUsuario = (): RoleModel[] | undefined => {
    let roles = roleState
    let id: Number = 99
    if (userState?.user!.role == rolesName.ADMINISTRADOR) {
      roles = roles?.filter((x) => x.Name == rolesName.EMPLEADO)
      roles?.push({ Id: id, Name: rolesName.CUENTA })
    } else if (userState?.user!.role == rolesName.SUPERADMINISTRADOR) {
      roles = roles?.filter((x) => x.Name == rolesName.ADMINISTRADOR)
    }
    if (roles) {
      if (
        usuarioState?.IdRole == 0 &&
        usuarioState?.RolesTipoCuenta?.length == 0
      ) {
        if (!isSaveForm) {
          dispatch(
            addUsuario({
              ...usuarioState,
              IdRole: roles!.length > 0 ? roles![0].Id : 0,
            }),
          )
        }
      }
    }
    return roles
  }

  //Select para los roles
  const roleSelectedBoxOptions = {
    items: getRolesTipoUsuario(),
    value: usuarioState?.IdRole,
    placeholder: 'Seleccione un tipo',
    displayExpr: 'Name',
    valueExpr: 'Id',
    readOnly: isNormalUserByIdRole(usuarioState!) == true ? false : true,
    onValueChanged: (event: any) => {
      if (event.previousValue != event.value && event.previousValue != null) {
        if (event.value != null) {
          const tipoUsuario = getRolesTipoUsuario()?.find(
            (x) => x.Id == event.value,
          )
          if (tipoUsuario != null) {
            if (tipoUsuario.Name == rolesName.CUENTA) {
              setIsCuenta(false)
            } else {
              setIsCuenta(true)
            }
          }
          handleUpdatedUsuario({
            IdRole: event.value != null ? event.value : usuarioState?.IdRole,
          })
        }
      }
    },
  }

  //Select para los perfiles
  const perfilSelectedBoxOptions = {
    items: perfilState.perfiles,
    value: usuarioState?.IdPerfil != null ? usuarioState?.IdPerfil : 0,
    placeholder: 'Seleccione un perfil',
    displayExpr: 'Nombre',
    valueExpr: 'Id',
    onValueChanged: (event: any) => {
      if (event.previousValue != event.value && event.previousValue != null) {
        if (event.value != null) {
          handleUpdatedUsuario({
            IdPerfil:
              event.value != null ? event.value : usuarioState?.IdPerfil,
          })
        }
      }
    },
  }

  const getRolesTipoCuenta = (): RoleModel[] | undefined => {
    let roles = roleState
    roles = roles?.filter(
      (x) =>
        x.Name == rolesName.COLABORADOR ||
        x.Name == rolesName.TRANSMITENTE ||
        x.Name == rolesName.CLIENTE,
    )
    return roles
  }

  const getRolesLits = (id: Number): RoleModel | undefined => {
    const role = roleState?.find((x) => x.Id == id)
    return role
  }

  const roleCuentaSelectedBoxOptions = {
    items: getRolesTipoCuenta(),
    value: 0, //usuarioState?.IdRole,
    placeholder: 'Seleccione un tipo',
    displayExpr: 'Name',
    valueExpr: 'Id',
    disabled: isCuenta,
    onValueChanged: (event: any) => {
      if (event.previousValue != event.value && event.previousValue != null) {
        if (event.value != null) {
          handleUpdatedUsuario({
            IdRole: event.value != null ? event.value : usuarioState?.IdRole,
          })
        }
      }
    },
  }

  const handleUpdatedUsuario = (property: any) => {
    if (property['IdRole'] != undefined) {
      if (property['IdRole'] == null) {
        return
      }
    }
    const usuario = {
      ...usuarioState,
      ...property,
    }
    if (usuario['IdRole'] == -1) {
      dispatch(
        addUsuario({
          ...new UsuarioModel(),
          IdRole: 0,
          IdSujeto: 0,
          RolesTipoCuenta: [],
        }),
      )
      setUpdatedForm(true)
    } else {
      if (usuario.RolesTipoCuenta?.length > 0) {
        if (usuario.IdRole != 99 && usuario.IdRole != 0) {
          if (!isSaveForm || usuario.Id) {
            dispatch(
              addUsuario({
                ...usuario,
                RolesTipoCuenta: [],
              }),
            )
            if (!usuario.Id || usuario.Id == 0) {
              setUpdatedForm(true)
            }
          }
        } else {
          const id: Number = 99
          let roles = roleState?.filter((x) => x.Name == rolesName.EMPLEADO)
          roles?.push({ Id: id, Name: rolesName.CUENTA })
          if (!isSaveForm || usuario.Id) {
            dispatch(
              addUsuario({
                ...usuario,
                IdRole: id,
              }),
            )
            if (!usuario.Id || usuario.Id == 0) {
              setUpdatedForm(true)
            }
          }
        }
      } else {
        if (!isSaveForm || usuario.Id) {
          dispatch(
            addUsuario({
              ...usuario,
              IdSujeto:
                usuario.IdSujeto == 0
                  ? sujetoState?.sujetos?.Datos[0].Id
                  : usuario.IdSujeto,
            }),
          )
          setUpdatedForm(true)
          // if (!usuario.Id || usuario.Id == 0) {
          //   setUpdatedForm(true)
          // }
        }
      }
    }
  }
  const usuarioFormRef: any = useRef(null)
  if (!props.showForm) {
    if (usuarioFormRef != null) {
      usuarioFormRef.current?.instance.resetValues()
    }
  }
  return (
    <>
      <div className={'content-block  responsive-paddings'}>
        <div className={'content-block  '}>
          <form
            className={'legalForm'}
            onSubmit={handleSubmit(onSubmit)}
            encType="multipart/form-data"
            style={{ width: '100%' }}
            autoComplete={'new-password'}
          >
            {/* <div
              style={{
                display: 'none',
              }}
            >
              <input type="password" />
            </div> */}
            <Form
              formData={formData.current}
              disabled={loading}
              ref={usuarioFormRef}
              readOnly={false}
              labelLocation={isLarge ? 'left' : 'top'}
              showValidationSummary={false}
              validationGroup="usuarioData"
              showColonAfterLabel={true}
              // autoComplete={'off'}
              //onFieldDataChanged={formFieldDataChanged}
            >
              <GroupItem colCount="2">
                <SimpleItem
                  dataField="Login usuario"
                  editorType={'dxTextBox'}
                  editorOptions={{
                    disabled: usuarioState?.Id ? true : false,
                    stylingMode: 'filled',
                    placeholder: 'Login usuario',
                    mode: 'text',
                    value: usuarioState?.NombreUsuario
                      ? usuarioState?.NombreUsuario
                      : '',
                    onValueChanged: (event: any) => {
                      //const user = userState?.user
                      //if (userState!.user!.UserName != event.value) {
                      if (event.event) {
                        handleUpdatedUsuario({
                          NombreUsuario: event.value.replace(/\s+$/, ''),
                        })
                        if (!isChangedState) dispatch(addDataFormChange(true))
                      }
                      // } else {
                      //   handleUpdatedUsuario({
                      //     NombreUsuario: '',
                      //   })
                      //   if (!isFirtsTimeShowForm) {
                      //     setFirtsTimeShowForm(true)
                      //   } else {
                      //     showToast(
                      //       'Estas itentando insertar tu nombre de usuario actual',
                      //       NotifyType.warning,
                      //       5000,
                      //     )
                      //   }
                      // }
                    },
                  }}
                >
                  {/* pattern="^[ A-Za-z0-9_@./#&+-]*$" */}
                  <PatternRule
                    pattern="^[a-zA-ZñÑáéíóúÁÉÍÓÚ\s]*$"
                    message="Caracters no validos para el usuario"
                  />
                  {usuarioState?.IdSujeto == 0 &&
                  usuarioState?.SujetoIndetification == '' ? (
                    <RequiredRule message="Caracters no validos para el usuario" />
                  ) : null}
                  <RequiredRule message="Debes introducir un nombre de usuario,debe ser único" />
                </SimpleItem>
                <SimpleItem
                  dataField="NIF/NIE/CIF"
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Número de identificación(NIF,NIE,CIF...)',
                    mode: 'text',
                    value: usuarioState?.IdentificationNumber
                      ? usuarioState?.IdentificationNumber
                      : '',
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedUsuario({
                          IdentificationNumber: event.value,
                        })
                        if (!isChangedState) dispatch(addDataFormChange(true))
                      }
                    },
                  }}
                >
                  <PatternRule
                    pattern="^([a-z]|[A-Z]|[0-9])[0-9]{7}([a-z]|[A-Z]|[0-9])$"
                    message="Debes introducir un numero de identificación NIF/CIF/NIE"
                  />
                  {usuarioState?.IdSujeto == 0 &&
                  usuarioState?.SujetoIndetification == '' ? (
                    <RequiredRule message="Debes introducir un numero de identificación NIF/CIF/NIE" />
                  ) : null}
                </SimpleItem>
                <SimpleItem
                  dataField={'Nombre'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Nombre',
                    mode: 'text',
                    value: usuarioState?.Nombre ? usuarioState?.Nombre : '',
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedUsuario({ Nombre: event.value })
                        if (!isChangedState) dispatch(addDataFormChange(true))
                      }
                    },
                  }}
                >
                  <RequiredRule message="Debes introducir un Nombre" />
                </SimpleItem>
                <SimpleItem
                  dataField={'Apellidos'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Apellidos',
                    mode: 'text',
                    value: usuarioState?.Apellidos
                      ? usuarioState?.Apellidos
                      : '',
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedUsuario({ Apellidos: event.value })
                        if (!isChangedState) dispatch(addDataFormChange(true))
                      }
                    },
                  }}
                ></SimpleItem>
                <SimpleItem
                  dataField={'Email'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Email',
                    mode: 'text',
                    value: usuarioState?.Email ? usuarioState?.Email : '',
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedUsuario({
                          Email: event.value.replaceAll(/\s/g, ''),
                        })
                        if (!isChangedState) dispatch(addDataFormChange(true))
                      }
                    },
                  }}
                >
                  <RequiredRule message="El campo es requerido" />
                  <EmailRule message="Debe introducir un email con un formato correcto" />
                </SimpleItem>
                <SimpleItem
                  dataField={'Telefono'}
                  editorType={'dxTextBox'}
                  editorOptions={{
                    stylingMode: 'filled',
                    placeholder: 'Telefono',
                    mode: 'text',
                    value: usuarioState?.Telefono ? usuarioState?.Telefono : '',
                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedUsuario({ Telefono: event.value })
                        if (!isChangedState) dispatch(addDataFormChange(true))
                      }
                    },
                  }}
                ></SimpleItem>
                {userState?.user?.UserName == usuarioState?.NombreUsuario ? (
                  <SimpleItem
                    dataField={'Tipo Usuarios'}
                    editorType={'dxTextBox'}
                    editorOptions={{
                      readOnly: true,
                      stylingMode: 'filled',
                      mode: 'text',
                      value: rolesName.ADMINISTRADOR,
                    }}
                  ></SimpleItem>
                ) : (
                  <SimpleItem
                    dataField="Tipo Usuario"
                    editorType="dxSelectBox"
                    editorOptions={roleSelectedBoxOptions}
                  ></SimpleItem>
                )}
                <SimpleItem
                  dataField="Gestoria"
                  editorType="dxSelectBox"
                  editorOptions={gestoriasSelectedBoxOptions}
                >
                  <RequiredRule message="Debes elegir al menos una Compañía" />
                </SimpleItem>
                {isAdministrador(userState!.user!) ? (
                  <SimpleItem
                    dataField="Tipo Cuenta"
                    editorOptions={{
                      readOnly:
                        isNormalUserByIdRole(usuarioState!) == true
                          ? false
                          : true,
                    }}
                  >
                    <TagBox
                      readOnly={
                        isNormalUserByIdRole(usuarioState!) == true
                          ? false
                          : true
                      }
                      dataSource={getRolesTipoCuenta()}
                      value={usuarioState?.RolesTipoCuenta}
                      displayExpr="Name"
                      valueExpr="Id"
                      disabled={isCuenta}
                      onValueChanged={(e) => {
                        handleUpdatedUsuario({ RolesTipoCuenta: e.value })
                      }}
                    />
                  </SimpleItem>
                ) : null}
                {isAdministrador(userState!.user!) ? (
                  <GroupItem colCount="2">
                    <SimpleItem
                      dataField={'Relación con'}
                      editorType={'dxTextBox'}
                      editorOptions={{
                        readOnly:
                          usuarioState?.NombreUsuario != '' &&
                          usuarioState?.Id != 0
                            ? true
                            : usuarioState.IdRole != 99 &&
                              usuarioState.IdRole != 0
                            ? getRolesLits(usuarioState?.IdRole!)?.Name !=
                                rolesName.EMPLEADO &&
                              getRolesLits(usuarioState?.IdRole!)?.Name !=
                                rolesName.ADMINISTRADOR &&
                              getRolesLits(usuarioState?.IdRole!)?.Name !=
                                rolesName.SUPERADMINISTRADOR
                              ? false
                              : true
                            : true
                            ? false
                            : true,
                        stylingMode: 'filled',
                        placeholder: 'Numero Identifación',
                        mode: 'text',
                        value: usuarioState?.SujetoIndetification
                          ? usuarioState?.SujetoIndetification
                          : '',
                        onValueChanged: (event: any) => {
                          if (event.event) {
                            handleUpdatedUsuario({
                              SujetoIndetification: event.value,
                            })
                            if (!isChangedState)
                              dispatch(addDataFormChange(true))
                          }
                        },
                      }}
                    >
                      {usuarioState?.IdSujeto == 0 &&
                      usuarioState?.IdentificationNumber == '' ? (
                        <RequiredRule message="Debes seleccionar relación o insertar un NIF valido" />
                      ) : null}
                    </SimpleItem>
                    <SimpleItem
                      editorType="dxSelectBox"
                      editorOptions={{
                        ...sujetoSelectedBoxOptions,
                        readOnly:
                          usuarioState?.NombreUsuario != '' &&
                          usuarioState?.Id != 0
                            ? true
                            : usuarioState.IdRole != 99 &&
                              usuarioState.IdRole != 0
                            ? getRolesLits(usuarioState?.IdRole!)?.Name !=
                                rolesName.EMPLEADO &&
                              getRolesLits(usuarioState?.IdRole!)?.Name !=
                                rolesName.ADMINISTRADOR &&
                              getRolesLits(usuarioState?.IdRole!)?.Name !=
                                rolesName.SUPERADMINISTRADOR
                              ? false
                              : true
                            : true
                            ? false
                            : true,

                        placeholder: 'No Seleccionado',
                      }}
                    >
                      {usuarioState?.SujetoIndetification == '' &&
                      usuarioState?.IdentificationNumber == '' ? (
                        <RequiredRule message="Debes elegir al menos una Compañía" />
                      ) : null}
                    </SimpleItem>
                  </GroupItem>
                ) : null}
                <SimpleItem
                  dataField={'Passwor'}
                  editorType={'dxTextBox'}
                  visible={false}
                  editorOptions={{
                    autocomplete: 'false',
                    stylingMode: 'filled',
                    mode: 'password',
                  }}
                >
                  <RequiredRule message="El campo es requerido" />
                  <Label visible={true} text="Contraseña" />
                </SimpleItem>
                {usuarioState?.Id == 0 ? (
                  <SimpleItem
                    dataField={'Passwor'}
                    editorType={'dxTextBox'}
                    editorOptions={{
                      autocomplete: 'false',
                      stylingMode: 'filled',
                      mode: 'password',
                      value: usuarioState?.Password,
                      onValueChanged: (event: any) => {
                        if (event.event) {
                          if (event.event) {
                            handleUpdatedUsuario({
                              Password: event.value,
                            })
                            if (!isChangedState)
                              dispatch(addDataFormChange(true))
                          }
                        }
                      },
                    }}
                  >
                    <RequiredRule message="El campo es requerido" />
                    <Label visible={true} text="Contraseña" />
                  </SimpleItem>
                ) : null}
              </GroupItem>
              <GroupItem colCount="2">
                <SimpleItem
                  dataField="Perfiles"
                  editorType="dxSelectBox"
                  editorOptions={perfilSelectedBoxOptions}
                ></SimpleItem>
                <SimpleItem
                  dataField={'Bloqueado'}
                  editorType={'dxCheckBox'}
                  editorOptions={{
                    stylingMode: 'filled',

                    value: usuarioState?.Enabled
                      ? usuarioState?.Enabled
                      : false,

                    onValueChanged: (event: any) => {
                      if (event.event) {
                        handleUpdatedUsuario({
                          Enabled: event.value,
                        })
                        if (!isChangedState) dispatch(addDataFormChange(true))
                      }
                    },
                  }}
                >
                  <Label visible={true} text="Bloqueado" />
                </SimpleItem>
              </GroupItem>

              <SimpleItem render={renderButtons} cssClass="alignBottom" />
            </Form>
          </form>
        </div>
      </div>
    </>
  )
}

export default UsuarioFormPage
