import {
  SujetoActionsTypes,
  ADD_PERFIL,
  CLEAR_PERFIL,
  PerfilState as PerfilState,
  ADD_PERFILES,
} from './perfilType'

const initialState: PerfilState = {
  perfil: undefined,
  perfiles: undefined,
}

export const perfilReducer = (
  state = initialState,
  action: SujetoActionsTypes,
): PerfilState => {
  switch (action.type) {
    case ADD_PERFIL: {
      return { ...state, perfil: action.payload }
    }
    case ADD_PERFILES: {
      return { ...state, perfiles: action.payload }
    }
    case CLEAR_PERFIL: {
      return { ...state, perfil: action.payload }
    }
    default:
      return state
  }
}
