import { DatoUsuarioCampoDatoModel } from '../models/datoUsuarioCampoModel'
import { DatoUsuarioModel } from '../models/datoUsuarioModel'

export interface DatoUsuarioState {
  datoUsuario: DatoUsuarioModel | undefined
  datosUsuarios: DatoUsuarioCampoDatoModel | undefined
}

export const ADD_DATOS_USUARIOS = 'ADD_DATOS_USUARIOS'
export const ADD_DATO_USUARIO = 'ADD_DATO_USUARIO'
export const CLEAR_DATO_USUARIO = 'CLEAR_DATO_USUARIO'
interface AddDatosUsuariosRequest {
  type: typeof ADD_DATOS_USUARIOS
  payload: DatoUsuarioCampoDatoModel | undefined
}
interface AddDatoUsuarioRequest {
  type: typeof ADD_DATO_USUARIO
  payload: Partial<DatoUsuarioModel> | undefined
}
interface ClearDatoUsuarioRequest {
  type: typeof CLEAR_DATO_USUARIO
  payload: DatoUsuarioModel | undefined
}

export type DatoUsuarioActionsTypes =
  | AddDatosUsuariosRequest
  | AddDatoUsuarioRequest
  | ClearDatoUsuarioRequest
