import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'
import { checkStatusCode } from 'shared-utils'
import { logger } from 'login-service'
import { UserModel } from 'shared-models'
import { authHeader } from 'shared-services'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { ModuloCampoDatoModel } from '../models/moduloModel/moduloCampoDatoModel'

const serviceName = 'ModulosService'
export const getModulos = async (
  user: UserModel,
): Promise<ModuloCampoDatoModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/Modulo`
    let ModuloData: ModuloCampoDatoModel = new ModuloCampoDatoModel()
    let datos
    const headers = {
      Authorization: user.Token,
      'My-Custom-Header': 'foobar',
    }
    await axios({
      url: url,
      method: 'GET',
      headers: headers,
    })
      .then((response) => {
        ModuloData = jsonConvert.deserializeObject(
          response.data,
          ModuloCampoDatoModel,
        )
        datos = response.data
      })
      .catch((error) => {
        showToast(
          'No se han podido obtener las compañias.',
          NotifyType.error,
          5000,
        )
      })

    return ModuloData
  } catch (err) {
    // Handle Error Here
    console.error(err)
  }
}
