import axios from 'axios'
import { JsonConvert, ValueCheckingMode } from 'json2typescript'
import { checkStatusCode, methodsEndPointType } from 'shared-utils'
import { UserModel } from 'shared-models'
import { authHeader } from 'shared-services'
import { NotifyType, showToast } from '../../../../utils/sharedUitls'
import { ModuloComponenteCampoDatoModel } from '../models/moduloComponenteModel/moduloComponenteCampoDatoModel'
import { ComponenteCampoDatoModel } from '../models/componenteModel/componenteDatoModelModel'
import { ComponenteModel } from '../models/componenteModel/componenteModel'

const serviceName = 'ComponentesService'
export const getComponentes = async (
  user: UserModel,
): Promise<ComponenteCampoDatoModel | undefined> => {
  try {
    let jsonConvert: JsonConvert = new JsonConvert()
    jsonConvert.valueCheckingMode = ValueCheckingMode.ALLOW_NULL

    let url = `${process.env.REACT_APP_API_URL_CORE}/Componente`
    let ComponenteData: ComponenteCampoDatoModel =
      new ComponenteCampoDatoModel()
    let datos
    await axios({
      url: url,
      method: methodsEndPointType.GET,
      headers: authHeader(user!),
    })
      .then((response) => {
        ComponenteData = jsonConvert.deserializeObject(
          response.data,
          ComponenteCampoDatoModel,
        )
        datos = response.data
      })
      .catch((error) => {
        showToast(
          'No se han podido obtener los componentes.',
          NotifyType.error,
          5000,
        )
      })

    return ComponenteData
  } catch (err) {
    console.error(err)
  }
}
