const routes = [
  {
    path: '/',
    component: LoginPage,
  },
  {
    path: '/expedientePage',
    component: ExpedientePage,
  },
  {
    path: '/login',
    component: LoginPage,
  },
  {
    path: '/loader',
    component: LoaderComponent,
  },
  {
    path: '/CambiarContraseña',
    component: CambiarContraseñaPage,
  },
  {
    path: '/AccesoSoloCalculadoraPage',
    component: AccesoSoloCalculadoraPage,
  },
  {
    path: '/Administracion',
    component: AdministracionTabsPage,
  },
  {
    path: '/createCompany',
    component: CompaniesAdministracionComponent,
  },
  {
    path: '/editCompany',
    component: CompanyFormPage,
  },
  {
    path: '/Companies',
    component: CompaniesPage,
  },
  {
    path: '/plantillas',
    component: PlantillasPage,
  },

  {
    path: '/tasks',
    component: tasksPage,
  },
  {
    path: '/profile',
    component: ProfilePage,
  },
  {
    path: '/sujetos',
    component: SujetoPage,
  },
  {
    path: '/sujetoForm',
    component: SujetoFormPage,
  },
  {
    path: '/usuarios',
    component: UsuarioPage,
  },
  {
    path: '/perfiles',
    component: PerfilPage,
  },
]

export default routes.map((route) => {
  return {
    ...route,
    component: withNavigationWatcher(route.component),
  }
})
